/*=====================
    33.Responsive css start
==========================*/

@media (max-width: 1600px) {
    .top-category {
        .top_box {
            .right-content {
                padding: 0px 10px;
            }
        }
        .slick-slide {
            > div {
                margin: 0 20px;
            }
        }
        .slick-list {
            margin-left: -20px;
            margin-right: -20px;
        }
    }
}

@media (min-width: 1461px) {
    .container {
        max-width: 1430px;
    }
}

@media (max-width: 1460px) {
    .category-wrapper {
        .category-wrap {
            .category-content {
                .bottom {
                    display: block;
                    margin-left: 0;
                    .coupon-code {
                        margin-left: 0;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
    .routes-section .routes-box .routes-content .top-bar {
        display: block;
    }
    h2 {
        line-height: 28px;
    }
    .cab-section {
        .cab-content {
            .car-select {
                ul {
                    width: 70%;
                }
                .btn {
                    width: 30%;
                    text-align: center;
                    font-size: 13px;
                }
            }
        }
    }
    .home_section {
        .home {
            .home-content {
                h1 {
                    margin-top: -10px;
                }
            }
        }
    }
    .mix-layout-section {
        .col-xl-10 {
            flex: 0 0 100%;
            max-width: 100%;
        }
    }
    .app-section {
        .app-box {
            padding: 50px;
        }
    }
    .service-section {
        .service-box {
            padding: 30px 20px;
        }
    }
    .testimonial-section {
        .testimonial {
            .right-part {
                margin-left: 10%;
            }
        }
    }
    .subscribe-footer {
        .service-left {
            .service-box {
                padding: 0 35px;
            }
        }
        .service-right {
            .input-group {
                input {
                    padding: 10px 18px;
                }
            }
            .btn-subscribe {
                padding: 8px 14px;
            }
        }
    }
    .subscribe_section {
        .subscribe-detail {
            padding-right: 25px;
        }
        .input-section {
            padding-left: 25px;
        }
    }
    .about-section {
        &.three-image {
            .about-text {
                padding-left: 0;
                h5,
                h3,
                p {
                    margin-bottom: 15px;
                }
                img {
                    width: 50%;
                }
            }
        }
        &.about_page {
            .image-section {
                .img-box {
                    .title-box {
                        padding: 15px;
                        min-width: 142px;
                    }
                }
            }
            .about-text {
                .buttons-about {
                    margin-top: 25px;
                }
                h2 {
                    span {
                        margin-top: 10px;
                    }
                }
                .title-3 {
                    margin-bottom: 25px;
                }
            }
        }
    }
    .ticket-section {
        .ticket-box {
            .content {
                padding: 30px 90px 30px 30px;
                .barcode-design {
                    right: 30px;
                    img {
                        height: 270px;
                    }
                }
            }
            .ticket-title {
                right: -13%;
            }
        }
        &.white-section {
            .ticket-box {
                .content {
                    .detail {
                        h4 {
                            margin-bottom: 10px;
                        }
                    }
                }
                .ticket-title {
                    right: -11%;
                }
            }
        }
    }
    .video-icon {
        width: 75px;
        height: 50px;
        span {
            border-top: 10px solid transparent;
            border-left: 20px solid white;
            border-bottom: 10px solid transparent;
        }
    }
    .video-section {
        .basic-section {
            h2 {
                margin-bottom: 25px;
            }
            h4 {
                margin-bottom: 35px;
            }
        }
    }
    .gallery-box {
        .gallery-content {
            h5 {
                margin-bottom: 10px;
                font-size: 16px;
            }
        }
    }
    .gallery_box {
        .gallery-content {
            bottom: 15px;
            left: 15px;
            h3 {
                margin-bottom: 5px;
                font-size: 20px;
            }
        }
        .view-cls {
            width: 30px;
            height: 30px;
            right: 15px;
            bottom: 15px;
        }
    }
    .blog-section {
        .blog-box {
            .img-part {
                img {
                    min-width: 260px;
                }
                .blog-date {
                    top: 20px;
                    left: 20px;
                    padding: 8px 10px;
                }
            }
            .blog-content {
                padding: 30px;
                p {
                    line-height: 1.3;
                }
            }
        }
    }
    .blog_section {
        .slick-slide {
            > div {
                margin: 0 15px;
            }
        }
        .slick-list {
            margin-left: -30px;
            margin-right: -30px;
        }
        .blog-wrap {
            .blog-details {
                padding: 30px 25px;
                h6 {
                    margin-bottom: 20px;
                }
                h5 {
                    margin-bottom: 10px;
                }
            }
            .blog-image {
                .blog-label {
                    padding: 10px 12px;
                    left: 25px;
                    bottom: 25px;
                }
            }
        }
        &.destination-section {
            .slick-slide {
                > div {
                    margin: 0 20px;
                }
            }
            .slick-list {
                margin-left: -20px;
                margin-right: -20px;
            }
            .destination-details {
                padding: 30px 20px;
            }
        }
    }
    .special-section {
        .slick-slide {
            > div {
                margin: 0 20px;
            }
        }
        .slick-list {
            margin-left: -20px;
            margin-right: -20px;
        }
        .special-box {
            .special-content {
                padding: 30px;
            }
        }
        &.grid-box {
            .special-box {
                .special-content {
                    padding: 20px;
                    h5 {
                        display: block;
                        span {
                            display: block;
                            margin-left: 0;
                        }
                    }
                    .tour-detail {
                        .include-sec {
                            ul {
                                li {
                                    font-size: 0;
                                }
                            }
                        }
                    }
                    &.restaurant-detail {
                        h5 {
                            span {
                                display: inline-block;
                            }
                        }
                    }
                }
            }
        }
    }
    .full-banner {
        .offer-text {
            left: -70%;
        }
    }
    .category-sec {
        .slick-slide {
            > div {
                margin: 0 15px;
            }
        }
        .slick-list {
            margin-left: -15px;
            margin-right: -15px;
        }
    }
    .category-wrapper {
        .category-wrap {
            .category-img {
                img {
                    width: 180px;
                    height: 180px;
                    border-radius: 20px;
                }
                .side-effect {
                    &:after {
                        left: 12px;
                    }
                    &:before {
                        left: 6px;
                    }
                }
            }
            .category-content {
                padding: 45px 30px 45px 156px;
                p {
                    display: block;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
        .slick-slide {
            > div {
                margin: 0 15px;
            }
        }
    }
    .top-category {
        padding: 0 35px;
        .top_box {
            .right-content {
                padding: 0px 20px;
            }
        }
    }
    .detail-section {
        .detail-box {
            padding: 0 30px;
        }
    }
    .routes-section {
        .routes-box {
            .routes-content {
                padding: 40px 20px 20px 20px;
            }
            .routes-img {
                .price-round {
                    width: 90px;
                    height: 90px;
                }
                .discount {
                    top: 20px;
                    left: 20px;
                }
            }
        }
    }
    .flight-detail {
        .flight-box {
            .middle-section {
                width: 69%;
            }
        }
    }
    .filter-section {
        .content {
            margin-left: -20px;
            margin-right: -20px;
            .grid-item {
                padding: 0 20px;
            }
        }
    }
    .menu-section {
        .menu-box {
            .bottom-bar {
                padding: 25px;
                .menu-bar {
                    img {
                        margin-right: 15px;
                        width: 50px;
                    }
                }
            }
        }
    }
    .book-table {
        .table-form {
            padding: 30px;
        }
    }
    .process-steps {
        .step-bg {
            .step-box {
                padding: 35px 25px;
            }
        }
    }
    .cab-full {
        .cab-search-section {
            .cab-search-box {
                .btn-solid {
                    padding: 2px 5px;
                }
            }
        }
        .left-bar {
            &.fixed-cls {
                width: 320px;
            }
        }
    }
    .scroll-body {
        padding-left: 320px;
    }
    .deals {
        .deals-box {
            .left-portion {
                flex: 0 0 33.33333%;
                max-width: 33.33333%;
            }
        }
    }
    .search-section {
        .search-box {
            .left-part {
                .search-body {
                    .quantity-right-plus,
                    .quantity-left-minus {
                        right: 10%;
                    }
                }
            }
        }
    }
    .video-slider {
        height: 180px;
    }
    footer {
        .footer {
            .footer-content {
                .footer-map {
                    iframe {
                        width: 100%;
                        height: 190px;
                    }
                }
                .contact-detail {
                    p {
                        margin-bottom: 20px;
                        -webkit-line-clamp: 2;
                    }
                    .contact-list {
                        li {
                            font-size: 14px;
                        }
                    }
                }
            }
            .footer-space {
                padding-left: 25px;
            }
        }
    }
    // inner pages
    .filter-panel {
        padding: 20px 25px;
    }
    .left-sidebar {
        .search-bar {
            padding: 25px;
            input {
                padding: 10px 10px 10px 60px;
            }
        }
        .middle-part {
            padding: 25px 25px;
        }
        .bottom-info {
            padding: 0 25px 25px;
        }
    }
    .list-view {
        .list-box {
            .list-content {
                h6 {
                    max-width: 300px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }
    }
    .top-filter-section {
        ul {
            .onclick-title {
                min-width: 130px;
            }
        }
    }
    .blog-inner {
        &.blog_section {
            .blog-list {
                .blog-wrap {
                    .blog-image {
                        width: 30%;
                    }
                    .blog-details {
                        width: 70%;
                    }
                }
            }
        }
    }
    .dashboard-section {
        .dashboard-box {
            .dashboard-detail {
                .card-payment {
                    .payment-card {
                        padding: 20px 10px;
                        min-height: auto;
                    }
                }
            }
        }
        .dashboard-sidebar {
            .profile-top {
                .profile-image {
                    .profile-edit {
                        right: 22%;
                    }
                }
            }
        }
    }
}

@media (max-width: 1460px) and (min-width: 991px) {
    .special-section {
        &.grid-box {
            .special-box {
                .special-content {
                    .bottom-section {
                        .price {
                            display: block;
                            .facility-detail {
                                margin-left: 0;
                                margin-top: 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1460px) and (min-width: 1199px) {
    .category-section {
        .category-box {
            .top-bar {
                left: 10px;
                top: 15px;
            }
            .like-cls {
                right: 10px;
                top: 15px;
            }
            .bottom-bar {
                left: 10px;
                bottom: 15px;
                .top {
                    h5 {
                        font-size: 16px;
                        margin-right: 8px;
                        max-width: 115px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                    h6 {
                        margin-left: 10px;
                    }
                }
            }
            &:hover {
                .bottom-bar {
                    bottom: 25px;
                }
            }
        }
    }
}

@media (max-width: 1367px) {
