        .effect_image {
            height: 80vh;
        }
    }
    .service_section {
        > div {
            &:nth-last-child(-n + 3) {
                margin-top: 50px;
            }
        }
    }
    .service-part {
        .service-wrapper {
            padding: 30px;
            h3 {
                margin-bottom: 8px;
            }
            h6 {
                margin-bottom: 15px;
            }
            p {
                margin-bottom: 24px;
            }
        }
    }
    .testimonial-section {
        .testimonial {
            padding-bottom: 90px;
        }
    }
    .subscribe-section {
        .input-group {
            .btn {
                padding: 14px;
            }
        }
    }
    .about-section {
        .about-text {
            padding-left: 20px;
            h3,
            h2 {
                margin-bottom: 25px;
            }
            h5 {
                margin-bottom: 5px;
            }
            p {
                line-height: 1.4;
                margin-bottom: 15px;
            }
        }
    }
    .blog_section,
    .special-section {
        &.section-b-space {
            padding-bottom: 60px;
        }
    }
    .category-sec {
        &.section-b-space {
            padding-bottom: 50px;
        }
    }
    .category-wrapper {
        &.section-b-space {
            padding-bottom: 60px;
        }
    }
    .category-bg {
        &.section-b-space {
            padding-bottom: 80px;
        }
    }
    .cab-full {
        .location-option {
            display: block !important;
            [type="radio"]:checked + label:before,
            [type="radio"]:not(:checked) + label:before {
                top: 0;
            }
            .form-check {
                + .form-check {
                    margin-top: 5px;
                    margin-top: 0;
                }
            }
        }
        .cab-search-section {
            .cab-search-box {
                .btn-solid {
                    margin-left: -15px;
                }
            }
        }
    }
    .deals {
        .deals-box {
            .left-portion {
                flex: 0 0 37%;
                max-width: 37%;
            }
        }
    }
    // inner pages
    .portfolio-creative {
        .content-sec {
            p {
                max-width: 90%;
            }
        }
    }
}

