                            .rating {
                                display: block;
                                span {
                                    display: block;
                                    margin-left: 0;
                                }
                            }
                        }
                    }
                }
                .content-title {
                    text-transform: capitalize;
                }
            }
        }
    }
    .flight-search {
        .responsive-detail {
            .details {
                span {
                    display: block;
                    font-size: 12px;
                    &.divider {
                        display: none;
                    }
                }
            }
        }
    }
    .contact_section {
        &.contact_right {
            .row {
                > div {
                    flex: 0 0 100%;
                    max-width: 100%;
                    &:last-child {
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
    .dashboard-section {
        .dashboard-box {
            .dashboard-detail {
                .booking-box {
                    .detail-middle {
                        .media {
                            .icon {
                                display: none;
                            }
                            .media-body {
                                margin-left: 0;
                            }
                        }
                    }
                }
                .card-payment {
                    .payment-card {
                        &.add-card {
                            .card-details {
                                min-height: 124px;
                            }
                        }
                    }
                }
            }
        }
        .dashboard-sidebar {
            .profile-top {
                .profile-image {
                    .profile-edit {
                        right: 26%;
                    }
                }
            }
        }
    }
    .hotel-single-section {
        .hotel-title-section {
            .hotel-name {
                .left-part {
                    .top {
                        .rating {
                            display: none;
                        }
                    }
                }
            }
        }
    }
    .hotel_title_section {
        .hotel-name {
            .left-part {
                .top {
                    .rating {
                        display: none;
                    }
                }
            }
        }
    }
    .top-banner {
        .top-banner-content {
            right: -40px;
        }
    }
    .video-slider {
        height: 188px;
    }
}

@media (max-width: 360px) {
    .video-slider {
        height: 146px;
    }
}

@media (max-width: 320px) {
    .cab-section {
        height: 110vh;
        .cab-content {
            height: 110vh;
        }
    }
}
