/*=====================
01. General css start
==========================*/

body {
    font-family: $font_0, $font_1;
    position: relative;
    background: white;
    font-size: 14px;
    color: black;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $font_0, $font_1;
    font-weight: normal;
}

h1 {
    font-size: calc(26px + (50 - 26) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 700;
    letter-spacing: 0.03em;
    line-height: 1.1;
    text-transform: uppercase;
}

h2 {
    font-size: calc(22px + (36 - 22) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 700;
    letter-spacing: 0.001em;
    line-height: 32px;
    text-transform: capitalize;
    color: black;
}

h3 {
    font-size: calc(20px + (24 - 20) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 400;
    letter-spacing: 0.03em;
    line-height: 1.2;
    color: black;
}

h4 {
    font-size: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 400;
    letter-spacing: 0.03em;
    line-height: 1.2;
    text-transform: capitalize;
}

h5 {
    font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 400;
    letter-spacing: 0.03em;
    line-height: 1.2;
}

h6 {
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 400;
    line-height: 1.2;
    letter-spacing: 0.03em;
}

ul {
    padding-left: 0;
    margin-bottom: 0;
}

li {
    display: inline-block;
    font-size: 14px;
    letter-spacing: 0.03em;
}

p {
    font-size: 14px;
    line-height: 18px;
    color: $grey-light;
    letter-spacing: 0.03em;
}

a {
    transition: 0.5s ease;
    letter-spacing: 0.03em;
    text-decoration: none;
    &:hover {
        text-decoration: none;
        transition: 0.5s ease;
    }
    &:focus {
        outline: none;
    }
}

label {
    letter-spacing: 0.03em;
}

button,
.btn {
    &:focus {
        outline: none;
        box-shadow: none;
    }
}

section,
.section-t-space {
    padding-top: 100px;
}

.section_space {
    padding-top: calc(30px + (100 - 30) * ((100vw - 320px) / (1920 - 320)));
}

.section-b-space {
    padding-bottom: 100px;
}

.medium-section {
    padding-top: 80px;
    padding-bottom: 80px;
}

.small-section {
