}

.media {
    display: flex;
    align-items: flex-start;
    .media-body {
        flex: 1;
    }
}

.radio_animated {
    cursor: pointer;
    margin-left: 0;
    position: relative;
    margin-right: 12px;
    &:before {
        transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
        transform: scale(0, 0);
        content: "";
        position: absolute;
        top: 0;
        left: 0.125rem;
        z-index: 1;
        width: 0.75rem;
        height: 0.75rem;
        background: $theme-color1;
        border-radius: 50%;
    }
    &:after {
        content: "";
        position: absolute;
        top: -0.25rem;
        left: -0.125rem;
        width: 1.25rem;
        height: 1.25rem;
        background: white;
        border: 2px solid #e8ebf2;
        border-radius: 50%;
    }
    &:checked:before {
        transform: scale(1, 1);
    }
}

.checkbox_animated {
    cursor: pointer;
    margin-left: -1.25rem;
    position: relative;
    margin-right: 12px;
    border: none;
    &:before {
        transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
        transform: rotate(-45deg) scale(0, 0);
        content: "";
        position: absolute;
        left: 0.25rem;
        top: 0.225rem;
        z-index: 1;
        width: 0.75rem;
        height: 0.375rem;
        border: 2px solid $theme-color1;
        border-top-style: none;
        border-right-style: none;
    }
    &:after {
        content: "";
        position: absolute;
        top: -0.125rem;
        left: 0;
        width: 1.3rem;
        height: 1.3rem;
        background: white;
        border: 2px solid #e8ebf2;
        cursor: pointer;
    }
    &:checked:before {
        transform: rotate(-45deg) scale(1, 1);
    }
}

.form-check {
    display: flex;
}

.input-group-prepend, .input-group-append {
    display: flex;
}

.mrg-cls {
    margin-bottom: -3px;
}

.rounded5 {
    border-radius: $radious5 !important;
}

.rounded10 {
    border-radius: $radious10 !important;
}

.rounded15 {
    border-radius: $radious15 !important;
}

.rounded20 {
    border-radius: $radious20 !important;
}

.rounded25 {
    border-radius: $radious25 !important;
}

.breadcrumb {
    padding: 0.75rem 1rem;
}

.zindex-1 {
    z-index: 1;
}

.row {
    div[class*="col-"], .col {
        position: relative;
    }
  }

/*Lazy load */

.blur-up {
    -webkit-filter: blur(5px);
    filter: blur(5px);
    transition: filter 400ms, -webkit-filter 400ms;
    &.lazyloaded {
        -webkit-filter: blur(0);
        filter: blur(0);
    }
}


/*=====================
1.1. Button start
==========================*/
.btn-group-showcase {
    margin-bottom: -20px;
    h3{
        margin-bottom: 15px;
    }
    .btn{
        margin-bottom: 20px;
        &+.btn{
            margin-left: 15px;            
        }
    }
}
.btn-cards{
    .card{
        margin-bottom: 30px;
    }
}
.btn {
    line-height: 20px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
    border-radius: 0;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    &.btn-lower {
        text-transform: capitalize;
    }
    &.btn-sm {
        padding: 8px 24px !important;
        text-transform: capitalize;
    }
    &.white-btn {
        background-color: white !important;
        color: $theme-color1 !important;
        box-shadow: none !important;
    }
    &.black-btn {
        background-color: black !important;
        color: white !important;
        box-shadow: 1px 11px 20px 0px rgba(black, 0.12) !important;
        &:hover {
            box-shadow: 1px 11px 20px 0px rgba(black, 0.22);
            border: 1px solid black;
            background-color: white !important;
            color: black !important;
        }
    }
    &.btn-solid {
        background-color: $theme-color1;
        color: white;
        padding: 9px 20px;
        box-shadow: 1px 11px 20px 0px rgba($theme-color1, 0.12);
        &:hover {
            transition: 0.3s ease-in-out;
            background-color: white;
            color: $theme-color1;
            border: 1px solid $theme-color1;
            box-shadow: 1px 11px 20px 0px rgba($theme-color1, 0.22);
        }
        &.btn-outline {
            background-color: transparent;
            border: 1px solid $theme-color1;
            color: $theme-color1;
            &:hover {
                color: white;
                background-color: $theme-color1;
            }
        }
        &.color2 {
            background-color: $theme-color2;
            box-shadow: 2.5px 4.33px 25px 0px rgba($theme-color2, 0.3);
            &:hover {
                background-color: white;
                color: $theme-color2;
                transition: all 0.5s ease;
                border: 1px solid $theme-color2;
                box-shadow: 1px 11px 20px 0px rgba($theme-color2, 0.22);
            }
        }
    }
    &.btn-curve {
        border-radius: 5px;
        background-color: $theme-color1;
        color: white;
        padding: 9px 20px;
        box-shadow: 1px 11px 20px 0px rgba($theme-color1, 0.12);
        &.btn-lower {
            text-transform: capitalize;
        }
    }
    &.btn-rounded {
        border-radius: 100px;
        color: white;
        padding: 10px 20px;
        &.color1 {
            background-color: $theme-color1;
            box-shadow: 1px 11px 20px 0px rgba($theme-color1, 0.12);
            &:hover {
                background-color: white;
                color: $theme-color1;
                transition: all 0.5s ease;
                border: 1px solid $theme-color1;
                box-shadow: 1px 11px 20px 0px rgba($theme-color1, 0.22);
            }
        }
        &.color2 {
            background-color: $theme-color2;
            box-shadow: 2.5px 4.33px 25px 0px rgba($theme-color2, 0.3);
            &:hover {
                background-color: white;
                color: $theme-color2;
                transition: all 0.5s ease;
                border: 1px solid $theme-color2;
                box-shadow: 1px 11px 20px 0px rgba($theme-color2, 0.22);
            }
        }
    }
    &:focus {
        box-shadow: none;
    }
}

.btn-outline {
    background-color: white;
    border: 1px solid $theme-color1;
    color: $theme-color1;
}

.btn-close {
    &:focus {
        box-shadow: none;
    }
}


