/*=====================
2. Header section css
==========================*/

header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    .menu {
        display: flex;
        margin: 0 auto;
        align-items: center;
        nav {
            margin: 0 auto;
        }
        .coupon-box {
            background-color: white;
            border-radius: 4px;
            padding: 3px;
            h6 {
                font-size: 14px;
                text-transform: capitalize;
                font-weight: 700;
                color: $theme-color1;
                margin-bottom: 0;
                border: 1px dashed rgba($theme-color1, 0.4);
                border-radius: 4px;
                padding: 5px 10px;
            }
        }
        .header-right {
            display: flex;
            align-items: center;
            .front-setting {
                background-color: rgba(255, 255, 255, 0.14);
                select {
                    border: none;
                    color: white;
                    -webkit-appearance: none;
                    padding: 5px 28px 4px 10px;
                    text-transform: uppercase;
                    font-size: 16px;
                    font-weight: 900;
                    background-color: transparent;
                    background: url(../images/icon/down.png) no-repeat 80%;
                    position: relative;
                    cursor: pointer;
                    option {
                        color: black;
                    }
                    &:focus {
                        outline: none;
                    }
                }
            }
            .user {
                background-color: white;
                padding: 6px 12px;
                i {
                    color: $theme-color2;
                    font-size: 12px;
                }
                &.user-light {
                    background-color: rgba(white, 0.14);
                    i {
                        color: white;
                    }
                }
                &.bg_dark {
                    background-color: $theme-color1;
                }
            }
            .setting {
                background-color: rgba(255, 255, 255, 0.14);
                padding: 6px 12px;
                display: none;
                position: relative;
                i {
                    color: white;
                    font-size: 12px;
                }
                .setting-open {
                    position: absolute;
                    width: 100%;
                    display: block;
                    width: 120px;
                    right: 0;
                    background-color: white;
                    bottom: -84px;
                    display: none;
                    li {
                        display: block;
                        select {
                            color: black;
                            font-weight: 400;
                            width: 100%;
                        }
                        +li {
                            margin-left: 0;
                        }
                    }
                    &.show {
                        display: block;
                    }
                }
            }
            .search-bar {
                background-color: rgba(255, 255, 255, 0.14);
                padding: 6px 12px;
                cursor: pointer;
                position: relative;
                i {
                    color: white;
                }
                .form-control-search {
                    top: 50px;
                    position: absolute;
                    transition: all 0.3s linear;
                    right: 0;
                    transform: translateY(-35px) scaleY(0);
                    opacity: 0;
                    visibility: hidden;
                    margin-bottom: 0;
                    input {
                        width: 260px;
                        background-color: rgba(255, 255, 255, 0.32);
                        border: none;
                        padding: 10px 10px 10px 70px;
                        border-radius: 0;
                        &:focus {
                            outline: none;
                        }
                    }
                    i {
                        position: absolute;
                        top: 16px;
                        left: 20px;
                        z-index: 1;
                        color: rgba(black, 0.3);
                    }
                    &:before {
                        position: absolute;
                        content: "";
                        width: 1px;
                        height: 25px;
                        background: #e8ebf2;
                        left: 50px;
                        top: 9px;
                    }
                    &.open {
                        transform: translateY(0) scaleY(1);
                        opacity: 1;
                        visibility: visible;
                        -webkit-transition: all 0.3s linear;
                        transition: all 0.3s linear;
                        z-index: 100;
                    }
                }
            }
            li {
                +li {
                    margin-left: 15px;
                }
            }
        }
    }
    &.overlay-white {
        background-color: rgba(white, 0.08);
    }
    &.overlay-black {
        background-color: rgba(black, 0.18);
    }
    &.overlay-transparent {
        background-color: transparent;
        box-shadow: none;
    }
    &.inner-page {
        border-bottom: 1px solid rgba(white, 0.05);
    }
    &.light_header {
        background-color: white;
        position: relative;
        z-index: unset;
        .main-navbar {
            .nav-menu {
                >li {
                    >a {
                        color: $grey-darker;
                    }
                }
            }
        }
        .menu {
            .header-right {
                .front-setting {
                    background-color: rgb(243, 243, 245);
                    select {
                        color: $grey-darker;
                        background: url(../images/icon/down-black.png) no-repeat 80%;
                    }
                }
                .user {
                    &.user-light {
                        background-color: rgb(243, 243, 245);
                        i {
                            color: black;
                        }
                    }
                }
