                        font-weight: 700;
                        text-transform: uppercase;
                        border-bottom: 1px solid #efefef;
                        cursor: pointer;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                    }
                }
                >li {
                    display: block;
                    float: none;
