                        padding: 10px 15px;
                        font-size: 16px;
                        font-weight: 700;
                        position: relative;
                        color: $grey-darker;
                        .sub-arrow {
                            right: 20px;
                            position: absolute;
                            &:before {
                                content: "+";
                                font-family: inherit;
                                position: relative;
                            }
                        }
                    }
                    .nav-submenu {
                        width: 100%;
                        padding: 0;
                        position: relative;
                        display: none;
                        opacity: 1;
                        visibility: visible;
                        &.opensubmenu {
                            display: block;
                        }
                        li {
                            .nav-sub-childmenu {
                                display: none;
                                position: relative;
                                right: 0;
                                width: 100%;
                                padding: 0;
                                &.opensubchild {
                                    display: block;
                                }
                                li {
                                    a {
                                        padding: 0 45px;
                                    }
                                }
                            }
                            a {
                                &:after {
                                    display: none;
                                }
                                &:hover {
                                    margin-left: 0;
                                    &:after {
                                        display: none;
                                    }
                                }
                            }
                            .level1 {
                                li {
                                    &:hover {
                                        .level2 {
                                            display: none;
                                        }
                                    }
                                }
                            }
                            &:hover {
                                .level1 {
                                    display: none;
                                }
                            }
                        }
                    }
                    .mega-menu-container {
                        display: none;
                        padding: 0;
                        &.opensubmenu {
                            display: block;
                        }
                        .mega-box {
                            width: 100%;
                            padding: 0;
                            .link-section {
                                .submenu-title {
                                    position: relative;
                                    h5 {
                                        margin-bottom: 0;
                                        font-weight: 400;
                                        line-height: 1.9;
                                        padding: 2px 25px;
                                    }
                                    .according-menu {
                                        top: 4px;
                                    }
                                }
                                .submenu-content {
                                    display: none;
                                    &.opensubmegamenu {
                                        display: block;
                                        padding: 0 45px;
                                    }
                                    ul {
                                        li {
                                            a {
                                                line-height: 1.9;
                                                &:hover {
                                                    margin-left: 0;
                                                    &:after {
                                                        display: none;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .nav-link {
                        &.active {
                            background-color: transparent;
                            box-shadow: none;
                            color: $grey-darker;
                            transition: all 0.5s ease;
                            &:hover {
                                color: $theme-color1;
                                transition: all 0.5s ease;
                            }
                        }
                    }
                    &:hover {
                        .nav-submenu {
                            //opacity: 0;
                            //visibility: hidden;
                            margin-top: 0;
                        }
                    }
                }
            }
        }
        .menu {
