                margin-left: auto;
                margin-right: calc(10px + (30 - 10) * ((100vw - 320px) / (1920 - 320)));
            }
        }
    }
    .mega-box {
        .link-section {
            .demo {
                ul {
                    li {
                        a {
                            span {
                                img {
                                    top: 30px;
                                    right: -60px;
                                }
                            }
