        }
    }
    .according-menu {
        position: absolute;
        right: 20px;
    }
    .main-menu {
        &.border-section {
            border: none;
        }
        .menu-left {
            .main-menu-right {
                .toggle-nav {
                    position: absolute;
                    z-index: 2;
