.cab-section {
    position: relative;
    background-image: url("../images/cab-bg.jpg");
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
    z-index: 1;
    .cab-content {
        height: 100vh;
        padding-top: 75px;
        display: flex;
        align-items: center;
        >div {
            width: 100%;
        }
        .call-section {
            display: inline-block;
            .call {
                padding: 12px 20px;
                border-radius: 50px;
                background-color: rgba(white, 0.15);
                align-items: center;
                display: flex;
                position: relative;
                z-index: 1;
                margin-bottom: 35px;
                i {
                    margin-right: 50px;
                    font-size: calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));
                    color: white;
                    padding-top: 4px;
                    padding-left: 4px;
                }
                h2 {
                    font-size: calc(16px + (40 - 16) * ((100vw - 320px) / (1920 - 320)));
                    color: white;
                    margin-bottom: 0;
                    font-weight: 700;
                    display: inline-block;
                }
                &:after {
                    content: "";
                    position: absolute;
                    width: 70px;
                    height: 70px;
                    border-radius: 100%;
                    background-color: $theme-color1;
                    z-index: -1;
                    left: 0;
                    box-shadow: 1px 11px 20px 0px rgba(239, 63, 62, 0.12);
                }
            }
        }
        h2 {
            font-size: calc(16px + (45 - 16) * ((100vw - 320px) / (1920 - 320)));
            text-transform: uppercase;
            color: white;
            font-weight: 400;
            line-height: 1;
            margin-bottom: 5px;
        }
        h3 {
            font-size: calc(14px + (24 - 14) * ((100vw - 320px) / (1920 - 320)));
            color: white;
            font-weight: 400;
            margin-bottom: 40px;
        }
        form {
            .form-control {
                border: none;
                padding: 15px 20px;
                border-radius: 0;
            }
            ::placeholder {
                color: rgba(black, 0.5);
                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                text-transform: capitalize;
                font-weight: 700;
            }
            .form-group {
                position: relative;
                margin-bottom: 25px;
                img {
                    position: absolute;
                    right: 0;
                    top: 0;
                    border-left: 1px solid #e5e5e5;
                    padding: 17px;
                    z-index: 1;
                    background-color: white;
                }
            }
            .gj-datepicker,
            .gj-timepicker {
                button {
                    width: 55px;
                    position: relative;
                    border: none;
                    background-color: white;
                    display: flex;
                    align-items: center;
                    border-left: 1px solid #e5e5e5 !important;
                    justify-content: center;
                    border-radius: 0;
                    &:hover {
                        box-shadow: none;
                    }
                    &:active {
                        background-color: white;
                        border-color: white;
                        color: #6c757d;
                    }
                    &:focus {
                        box-shadow: none !important;
                    }
                    i {
                        position: relative;
                        top: 0;
                        left: 0;
                        opacity: 0.2;
                    }
                }
            }
        }
        .radio-form {
            margin-bottom: 25px;
            label {
                margin-right: 20px;
                +label {
                    margin-left: 30px;
                }
            }
            [type="radio"]:checked,
            [type="radio"]:not(:checked) {
                position: absolute;
                left: -9999px;
            }
            [type="radio"]:checked+label,
            [type="radio"]:not(:checked)+label {
                position: relative;
                padding-left: 30px;
                cursor: pointer;
                display: inline-block;
                font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                color: white;
                text-transform: capitalize;
                font-weight: 700;
            }
            [type="radio"]:checked+label:before,
            [type="radio"]:not(:checked)+label:before {
                content: "";
                position: absolute;
                left: 0;
                top: 3px;
                width: 18px;
                height: 18px;
                border: 4px solid white;
                border-radius: 100%;
                transition: all 0.5s ease;
            }
            [type="radio"]:checked+label:before {
                background-color: white;
                border: 4px solid $theme-color1;
                transition: all 0.5s ease;
            }
        }
        .car-select {
            display: flex;
            ul {
                display: flex;
                width: 75%;
                li {
                    background-color: white;
                    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                    color: rgba(black, 0.5);
                    padding: 10px 0;
                    border-radius: 4px;
                    text-transform: capitalize;
                    font-weight: 700;
                    transition: all 0.5s ease;
                    width: 100%;
                    margin-right: 25px;
                    text-align: center;
                    cursor: pointer;
                    &.active {
                        color: $theme-color1;
                        transition: all 0.5s ease;
                    }
                }
            }
            .btn {
                width: 25%;
                text-align: center;
                font-size: 14px;
                padding: 10px 20px;
                display: flex;
                align-items: center;
                border-radius: 4px;
                font-weight: 900;
                justify-content: center;
                height: 100%;
                line-height: 26px;
            }
        }
    }
    &.flight-section {
        background-image: url("../images/bg-flight.png");
        .cloud {
            height: 100%;
            bottom: 0;
            width: 100%;
            position: absolute;
            z-index: -1;
        }
        .cab-content {
            padding-top: 86px;
            .radio-form{
                margin-bottom: 5px;
            }
            .top-cls {
                font-size: 14px;
                color: white;
                text-transform: uppercase;
                margin-bottom: 30px;
                background-color: rgba(white, 0.2);
                padding: 2px 16px;
                display: inline-block;
                border-radius: 30px;
                font-weight: 800;
            }
            h2 {
                margin-bottom: 10px;
            }
            h3 {
                font-size: calc(14px + (35 - 14) * ((100vw - 320px) / (1920 - 320)));
                font-weight: 800;
                text-transform: uppercase;
            }
            .btn-rounded {
                padding: 14px 32px;
            }
            .form-group {
                &.row {
                    margin-left: -10px;
                    margin-right: -10px;
                    .col {
                        padding-left: 10px;
                        padding-right: 10px;
                    }
                }
            }
            .gj-datepicker,
            .gj-timepicker {
               ::placeholder {
                font-size: 14px;
            }
        }
    }
}
