                color: white;
                font-weight: 900;
                font-size: calc(18px + (35 - 18) * ((100vw - 320px) / (1920 - 320)));
                line-height: 1;
                opacity: 0.5;
            }
            &.slick-active {
                .dot {
                    font-size: calc(20px + (50 - 20) * ((100vw - 320px) / (1920 - 320)));
                    opacity: 1;
                }
            }
        }
