/*=====================
4. Slick slider css
==========================*/

.no-arrow {
    .slick-next,
    .slick-prev {
        display: none !important;
    }
}

.slick-slider {
    line-height: 1;
}

.arrow-classic {
    .slick-next,
    .slick-prev {
        &:before {
            font-family: $font-themify;
            color: #dfdfdf;
            background-color: white;
            opacity: 1;
            padding: 8px;
            box-shadow: 5px 8.66px 61px 0px rgba(0, 0, 0, 0.24);
            border-radius: 12px;
            font-size: 18px;
            transition: all 0.5s ease;
        }
        &:hover {
            &:before {
                color: $theme-color1;
                transition: all 0.5s ease;
            }
        }
    }
    .slick-prev {
        left: -100px;
        &:before {
            content: "\e64a";
        }
    }
    .slick-next {
        right: -88px;
        &:before {
            content: "\e649";
        }
    }
}

.arrow-dark {
    .slick-prev {
        left: 1px;
        z-index: 1;
        transition: all 0.5s ease;
        background-color: transparent;
        height: 36px;
        &:before {
            content: "\f053";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
        }
    }
    .slick-next {
        right: 0;
        z-index: 1;
        transition: all 0.5s ease;
        background-color: transparent;
        height: 36px;
        &:before {
            content: "\f054";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
        }
    }
    .slider-nav {
        margin-top: 3px;
        .slick-slide {
            margin: 0 3px;
            cursor: pointer;
        }
        .slick-list {
            margin-left: -3px;
            margin-right: -3px;
        }
    }
    &:hover {
        .special-img {
            .bg-size {
                transform: scale(1);
            }
        }
        .slick-next,
        .slick-prev {
            background-color: #252525;
            transition: all 0.5s ease;
        }
    }
}

.variable-width-height {
    .slick-slide {
        img {
            height: calc(280px + (400 - 280) * ((100vw - 320px) / (1920 - 320)));
        }
    }
}

.variable-width {
    margin-bottom: -4px;
}


/*=====================
5. Instagram css
==========================*/

.instgram-slider {
    .slick-slide {
        margin-bottom: -4px;
    }
