.ticket-section {
    .ticket-box {
        position: relative;
        .image-box {
            position: relative;
            overflow: hidden;
            box-shadow: 5px 8.66px 22px 0px rgba(black, 0.3);
            img {
                transition: all 0.5s ease;
            }
            &:after {
                content: "";
                position: absolute;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(black, 0.2);
                opacity: 0;
                top: 0;
                z-index: 0;
            }
            &:hover {
                &:after {
                    opacity: 1;
                }
                img {
                    transform: scale(1.04);
                    transition: all 0.5s ease;
                }
            }
        }
        .content {
            padding: 50px 150px 50px 50px;
            background-color: white;
            position: absolute;
            left: -56%;
            top: 50%;
            transform: translateY(-50%);
            box-shadow: 11px 4px 40px 0px rgba(0, 0, 0, 0.11);
            .detail {
                max-width: 395px;
                h4 {
                    font-size: calc(14px+(16 - 14) * ((100vw - 320px) / (1920 - 320)));
                    font-weight: 600;
                    color: rgba(black, 0.6);
                    margin-bottom: 15px;
                    span {
                        color: $theme-color1;
                        font-weight: 900;
                        font-size: calc(16px + (27 - 16) * ((100vw - 320px) / (1920 - 320)));
                    }
                }
                h3 {
                    font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
                    color: black;
                    font-weight: 900;
                    text-transform: capitalize;
                    margin-bottom: 18px;
                }
                p {
                    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                    margin-bottom: 25px;
                    line-height: 1.5;
                    color: $grey-light2;
                    font-weight: 600;
                }
            }
            .barcode-design {
                position: absolute;
                right: 50px;
                top: 50%;
                transform: translateY(-50%);
            }
            &:after {
                content: "";
                position: absolute;
                background-image: url(../images/zig-zag.png);
                right: -10px;
                height: 100%;
                width: 10px;
                top: 0;
            }
            &:before {
                content: "";
                position: absolute;
                background-image: url(../images/zig-zag.png);
                left: -10px;
                height: 100%;
                width: 10px;
                top: 0;
                transform: rotate(180deg);
            }
        }
        .ticket-title {
            position: absolute;
            top: 50%;
            transform: translateY(-50%) rotate(90deg);
            right: -11%;
            display: flex;
            z-index: 1;
            h6 {
                background-color: white;
                padding: 10px;
                font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                margin-bottom: 0;
                text-transform: capitalize;
                font-weight: 800;
            }
            span {
                background-color: red;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 10px;
                color: white;
            }
        }
    }
    .slick-prev,
    .slick-next {
        font-family: $font-themify;
        right: 40px;
        left: unset;
        z-index: 1;
        transition: all 0.5s ease;
        &:before {
            color: $theme-color1;
            background-color: white;
            opacity: 1;
            padding: 10px;
        }
        &:hover {
            box-shadow: rgba(0, 0, 0, 0.5) 5px 8.66px 40px 0px;
            transition: all 0.5s ease;
        }
    }
    .slick-next {
        top: calc(50% - 30px);
        &:before {
            font-family: $font-themify;
            content: "\e649";
        }
    }
    .slick-prev {
        top: calc(50% + 30px);
        &:before {
            font-family: $font-themify;
            content: "\e64a";
        }
    }
    &.white-section {
        .ticket-box {
            .ticket-title {
                right: -9%;
            }
            .content {
                .detail {
                    h4 {
                        margin-bottom: 20px;
                    }
                    h2 {
                        font-weight: 800;
                        margin-bottom: 10px;
                    }
                    h6 {
                        font-size: 14px;
                        color: $grey-10;
                        text-transform: capitalize;
                        font-weight: 800;
                        margin-bottom: 10px;
                        span {
                            color: $theme-color1;
                        }
                    }
                }
            }
        }
        .slick-prev,
        .slick-next {
            box-shadow: rgba(0, 0, 0, 0.5) 5px 8.66px 40px 0px;
            &:hover {
                box-shadow: rgba(0, 0, 0, 0.8) 5px 8.66px 40px 0px;
                transition: all 0.5s ease;
            }
        }
    }
}

.video-section {
