.video_section {
    padding: 138px 0;
    position: relative;
    overflow: hidden;
    .video-image {
        position: relative;
        z-index: 1;
        img {
            border-radius: 30px;
            transition: all 0.5s ease;
        }
        .video-icon {
            box-shadow: -1px 0px 25px 7px rgba(black, 0.5);
            transition: all 0.5s ease;
            &:hover {
                transform: translate(-50%, -50%) scale(1.06);
                transition: all 0.5s ease;
            }
        }
        .side-effect {
            &:after {
                content: "";
                position: absolute;
                top: 15px;
                height: calc(100% - 30px);
                background-color: rgba(white, 0.12);
                border-radius: 30px;
                width: calc(100% + 30px);
                left: -15px;
                z-index: -1;
                transition: all 0.5s ease;
            }
            &:before {
                content: "";
                position: absolute;
                top: 35px;
                height: calc(100% - 70px);
                background-color: rgba(white, 0.05);
                border-radius: 30px;
                width: calc(100% + 60px);
                left: -30px;
                z-index: -1;
                transition: all 0.5s ease;
            }
        }
        &:hover {
            img {
                transform: translateY(-25px);
                transition: all 0.5s ease;
            }
            .side-effect {
                &:after {
                    left: 10px;
                    width: calc(100% + 10px);
                    top: 0;
                    height: calc(100% - 10px);
                    transition: all 0.5s ease;
                }
                &:before {
                    width: 100%;
                    top: 35px;
                    left: 38px;
                    height: calc(100% - 30px);
                    transition: all 0.5s ease;
                }
            }
        }
    }
    .video-content {
        display: flex;
        align-items: center;
        height: 100%;
        h5 {
            color: white;
            font-weight: 700;
            text-transform: unset;
            margin-bottom: 30px;
            margin-top: -3px;
            font-style: italic;
            span {
                color: $theme-color1;
            }
        }
        h2 {
            font-size: calc(22px + (60 - 22) * ((100vw - 320px) / (1920 - 320)));
            text-transform: capitalize;
            font-weight: 700;
            color: white;
            margin-bottom: 26px;
            span {
                font-size: calc(22px + (40 - 22) * ((100vw - 320px) / (1920 - 320)));
                padding-left: 20px;
                margin-left: 5px;
                position: relative;
                &:after {
                    content: "";
                    position: absolute;
                    left: 0;
                    width: 3px;
                    height: 15px;
                    top: 50%;
                    transform: translateY(-50%);
                    background-color: white;
                }
            }
        }
        p {
            font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
            color: rgba(white, 0.7);
            line-height: 1.6;
            margin-bottom: 24px;
            padding-right: 10px;
        }
        .bottom-section {
            display: flex;
            align-items: center;
            margin-top: 35px;
            .timer {
                margin-bottom: 35px;
            }
            .info-btn {
                margin-left: 25px;
                h6 {
                    font-size: 14px;
                    color: white;
                    font-weight: 700;
                    text-transform: uppercase;
                    margin-bottom: 0;
                    background-color: rgba(white, 0.05);
                    border-radius: 50px;
                    padding: 10px 16px;
                }
                &.red-info {
                    margin-left: 10px;
                    h6 {
                        color: $theme-color1;
                    }
                }
            }
        }
    }
    .offer-text {
        position: absolute;
        right: -90px;
        top: 41%;
        h6 {
            color: white;
            transform: rotate(90deg);
            text-transform: uppercase;
            letter-spacing: 0.3em;
            font-weight: 700;
            position: relative;
            span {
                position: relative;
                display: inline-block;
                animation: ani 1s infinite alternate cubic-bezier(0.86, 0, 0.07, 1);
                &:nth-last-child(1n) {
                    animation-delay: -0.1666666667s;
                }
                &:nth-last-child(2n) {
                    animation-delay: -0.3333333333s;
                }
                &:nth-last-child(3n) {
                    animation-delay: -0.5s;
                }
            }
            &:after {
                content: "";
                position: absolute;
                width: 80px;
                height: 3px;
                background-color: white;
                top: 5px;
                right: -100px;
                background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(231, 56, 39, 0) 100%);
            }
        }
    }
}

