/*=====================
16. Filter section css
==========================*/

.filter-section {
    background-image: url("../images/restaurant/background.png");
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    .filters {
        position: relative;
        .filter-btn {
            border-radius: 19px;
            background-color: white;
            box-shadow: 0px 15px 40px 0px rgba(193, 0, 38, 0.25);
            padding: 10px 25px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            text-transform: capitalize;
            h6 {
                color: $theme-color1;
                margin-bottom: 0;
                line-height: 1;
                text-transform: capitalize;
                font-weight: 800;
                padding-right: 15px;
                position: relative;
                i {
                    position: absolute;
                    right: 0;
                    top: -3px;
                }
            }
        }
        .filter-content {
            position: absolute;
            top: 45px;
            right: 0;
            width: 100%;
            z-index: 1;
            background-color: rgb(190, 0, 37);
            box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
            h4 {
                margin-bottom: 0;
                text-align: center;
                background-color: rgba(black, 0.5);
                margin-top: 5px;
                padding: 5px;
                cursor: pointer;
            }
            li {
                display: block;
                color: white;
                margin-top: 6px;
                text-align: center;
                text-transform: capitalize;
                cursor: pointer;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
    .top-section {
        display: flex;
        align-items: center;
        margin-bottom: 70px;
        .title {
            h2 {
                font-size: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
                color: white;
                text-transform: uppercase;
                font-weight: 800;
            }
            h6 {
                font-size: 14px;
                color: white;
                opacity: 0.5;
                margin-bottom: 0;
            }
        }
        .coupon {
            margin: 0 auto;
            h6 {
                text-transform: capitalize;
                color: white;
                padding: 5px 10px;
                border: 1px dashed rgba(white, 0.5);
                border-radius: 25px;
                margin-bottom: 0;
            }
        }
    }
    .content {
        margin-left: -40px;
        margin-right: -40px;
        .grid-item {
            padding: 0 40px;
            .img-part {
                position: relative;
                img {
                    border-radius: 25px;
                    width: 100%;
                    box-shadow: 1px 10px 14px 1px rgba(0, 0, 0, 0.16);
                }
                .up-btn {
                    background-color: $theme-color1;
                    box-shadow: 0px 15px 25px 0px rgba(0, 0, 0, 0.5);
                    position: absolute;
                    width: 56px;
                    height: 56px;
                    border-radius: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    left: 50%;
                    transform: translateX(-50%) scale(0.5);
                    bottom: 15px;
                    opacity: 0;
                    transition: all 0.5s ease;
                    i {
                        color: white;
                        font-size: 18px;
                    }
                }
                &:after {
                    width: 70%;
                    height: 70%;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    background-color: black;
                    opacity: 0.702;
                    position: absolute;
                    //content: "";
                }
            }
            .filter-detail {
                opacity: 0.3;
                transition: all 0.5s ease;
                h6 {
                    font-size: 14px;
                    font-weight: 800;
                    text-transform: uppercase;
                    color: white;
                    letter-spacing: 0.3em;
                    margin-top: 15px;
                    margin-bottom: 6px;
                }
                .rating {
                    i {
                        font-size: 10px;
                    }
                }
            }
            &:hover {
                .img-part {
                    .up-btn {
                        transform: translateX(-50%) scale(1);
                        opacity: 1;
                        transition: all 0.5s ease;
                    }
                }
                .filter-detail {
                    opacity: 1;
                    transition: all 0.5s ease;
                }
            }
        }
    }
}


/*=====================
17. full banner section css
==========================*/

.full-banner {
    padding: 120px 0;
    position: relative;
    overflow: hidden;
    .banner-content {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
