                color: $theme-color1;
            }
        }
        h2 {
            font-size: calc(20px + (60 - 20) * ((100vw - 320px) / (1920 - 320)));
            text-transform: capitalize;
            font-weight: 700;
            color: white;
            margin-bottom: 30px;
            span {
                font-size: calc(18px + (40 - 18) * ((100vw - 320px) / (1920 - 320)));
            }
        }
        p {
            font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
            color: rgba(white, 0.8);
            line-height: 1.5;
            margin-bottom: 25px;
        }
        .price {
            font-size: calc(18px + (40 - 18) * ((100vw - 320px) / (1920 - 320)));
            del {
                font-size: calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)));
                color: rgba(255, 255, 255, 0.9);
                font-weight: 400;
            }
        }
        .bottom-section {
            display: flex;
            align-items: center;
            margin-top: 35px;
            .info-btn {
                margin-left: 25px;
                h6 {
                    font-size: 14px;
                    color: white;
                    font-weight: 700;
                    text-transform: uppercase;
                    margin-bottom: 0;
                    background-color: rgba(white, 0.05);
                    border-radius: 50px;
                    padding: 10px 16px;
                }
                &.red-info {
                    margin-left: 10px;
                    h6 {
                        color: $theme-color1;
                    }
                }
            }
        }
    }
    .offer-text {
        position: absolute;
        top: 20%;
        left: -64%;
        .offer {
            padding: 3px 5px;
            background-color: $theme-color1;
            color: white;
            font-size: 10px;
            text-transform: uppercase;
            border-radius: 25px;
            font-weight: 700;
        }
        h6 {
            color: white;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            font-weight: 700;
            position: relative;
            margin-top: 10px;
            span {
                position: relative;
                display: inline-block;
                animation: ani 1s infinite alternate cubic-bezier(0.86, 0, 0.07, 1);
                &:nth-last-child(1n) {
                    animation-delay: -0.1666666667s;
                }
                &:nth-last-child(2n) {
                    animation-delay: -0.3333333333s;
                }
                &:nth-last-child(3n) {
                    animation-delay: -0.5s;
                }
            }
            &:after {
                content: "";
                position: absolute;
                width: 80px;
                height: 3px;
                background-color: white;
                top: 5px;
                right: -100px;
                background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(231, 56, 39, 0) 100%);
            }
        }
    }
    &.banner-section {
        background-image: url(../images/flights/bg-2.jpg);
        background-size: cover;
        background-position: center center;
        .place-image {
            position: relative;
            .animation-place {
                .plane-l {
                    animation: mover 1s infinite alternate;
                    position: absolute;
                    top: 22%;
                    left: -11%;
                    img {
                        width: 70%;
                    }
                }
                .plane-r {
                    position: absolute;
                    right: 7%;
                    top: 0;
                    animation: mover 1.1s infinite alternate;
                }
                .cloud-l {
                    position: absolute;
                    top: 0;
                    left: 7%;
                    img {
                        width: 75%;
                    }
                }
                .cloud-r {
                    top: 0;
                    position: absolute;
                    right: 42%;
                }
            }
        }
    }
    &.small-section {
        padding: 30px 0;
    }
    &.padding-cls {
        padding-top: calc(90px + (200 - 90) * ((100vw - 320px) / (1920 - 320)));
        padding-bottom: calc(90px + (200 - 90) * ((100vw - 320px) / (1920 - 320)));
    }
    &.overlay-banner {
        background-blend-mode: overlay;
        background-color: rgba(255, 255, 255, 0.7);
        .banner-content {
            h2 {
                color: black;
            }
            h5 {
                color: rgba(black, 0.80);
            }
            p {
                color: rgba(black, 0.70);
            }
            .bottom-section {
                .info-btn {
                    h6 {
                        color: black;
                        background-color: rgba(black, 0.05);
                    }
                }
            }
        }
    }
}

