            background-image: url("../images/flights/sky2.jpg");
            background-size: cover;
            background-repeat: no-repeat;
            background-blend-mode: overlay;
            background-color: white;
            transition: all 0.5s ease;
            margin: 15px 0;
            .upper-part {
                margin-bottom: 15px;
                min-height: 120px;
                h2 {
                    font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
                }
                h5 {
                    font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
                }
                h6,
                h2,
                h5 {
                    color: black;
                }
            }
            &:hover {
                background-color: rgba(white, 0.8);
                transition: all 0.5s ease;
            }
        }
        .slick-slide {
            >div {
                margin: 0 15px !important;
            }
        }
        .slick-list {
            margin-left: -15px !important;
            margin-right: -15px !important;
        }
        .slick-prev {
            top: -25px;
            right: 30px;
            left: unset;
            &:before {
                opacity: 1;
                color: rgba($theme-color1, 0.3);
                content: "\f053";
                font-family: "Font Awesome 5 free";
                font-weight: 900;
            }
            &:hover {
                &:before {
                    color: rgba($theme-color1, 0.7);
                }
            }
        }
        .slick-next {
            top: -25px;
            right: 0;
            left: unset;
            &:before {
                opacity: 1;
                color: rgba($theme-color1, 0.3);
                content: "\f054";
                font-family: "Font Awesome 5 free";
                font-weight: 900;
            }
            &:hover {
                &:before {
                    color: rgba($theme-color1, 0.7);
                }
            }
        }
    }
}

.cloud {
    animation: slide 50s linear infinite;
    background-repeat: repeat !important;
}

.price-section {
    .container-fluid {
        padding-left: 90px;
        padding-right: 90px;
    }
    .price-box {
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        .price-img {
            flex: 0 0 42%;
            max-width: 42%;
            overflow: hidden;
            border-radius: 25px 0 0 25px;
            position: relative;
            .bg-size {
                height: 100%;
            }
            img {
                border-radius: 25px 0 0 25px;
                transition: all 0.5s ease;
                width: 100%;
            }
            .label {
                position: absolute;
                left: 20px;
                top: 20px;
                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                padding: 1px 12px;
                background-color: $theme-color1;
                border-radius: 25px;
                color: white;
                margin-bottom: 0;
                text-transform: capitalize;
                font-weight: 700;
            }
        }
        .price-content {
            flex: 0 0 58%;
            max-width: 58%;
            padding: calc(10px + (34 - 10) * ((100vw - 320px) / (1920 - 320)));
            background-color: white;
            box-shadow: 2.5px 4.33px 25px 0 rgba(0, 0, 0, 0.05);
            border-radius: 0 25px 25px 0;
            .price-title {
                display: flex;
                align-items: center;
                margin-bottom: calc(5px + (15 - 5) * ((100vw - 320px) / (1920 - 320)));
                margin-top: -4px;
                position: relative;
                h3 {
                    text-transform: capitalize;
                    font-weight: 800;
                    margin-bottom: 0;
                    transition: all 0.5s ease;
                    color: $grey-3;
                    letter-spacing: 0.001em;
                    &:hover {
                        color: $theme-color2;
                        transition: all 0.5s ease;
                    }
                }
                h6 {
                    font-size: 14px;
                    color: $grey-13;
                    text-transform: capitalize;
                    font-weight: 700;
                    margin-bottom: 0;
                    padding: 7px 10px;
                    margin-left: 10px;
                    background-color: $grey-4;
                    border-radius: 20px;
                }
                .like-cls {
                    i {
                        position: absolute;
                        font-size: 16px;
                        right: 0;
                        top: -3px;
                        color: $red-heart;
                        background-color: rgba($red-heart, 0.05);
                        padding: 6px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 100%;
                        .effect {
                            border-radius: 50%;
                            position: absolute;
                            height: 100%;
                            width: 100%;
                            top: 0;
                            right: 0;
                            transition: height 5s ease, width 5s ease;
                            z-index: 0;
                            opacity: 0;
                            &:after {
                                content: "";
                                display: block;
                                position: absolute;
                                top: 0;
                                right: 0;
                                bottom: 0;
                                left: 0;
                                border-radius: 50%;
                                border: 1px solid $red-heart;
                                animation: effect 2s linear 1s infinite;
                            }
                        }
                    }
                }
            }
            .rating {
                margin-bottom: calc(8px + (25 - 8) * ((100vw - 320px) / (1920 - 320)));
            }
            .price {
                display: flex;
                margin-bottom: 10px;
                a {
                    margin-right: auto;
                    h6 {
                        font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                        font-weight: 800;
                        margin-right: auto;
                        color: #565656;
                        text-transform: capitalize;
                        margin-bottom: 0;
                        transition: all 0.5s ease;
                        &:hover {
                            color: $theme-color2;
                            transition: all 0.5s ease;
                        }
                    }
                }
                span {
                    margin: 0 auto;
                    font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (1920 - 320)));
                    text-transform: uppercase;
                    color: $theme-color1;
                    font-weight: 800;
                }
                h5 {
                    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                    font-weight: 600;
                    margin-left: auto;
                    color: $theme-color2;
                    margin-bottom: 0;
                    span {
                        color: $grey-13;
                        margin-right: 10px;
                        font-weight: 700;
                        text-transform: capitalize;
                    }
                }
            }
        }
        &:hover {
            .price-img {
                img {
                    transform: scale(1.05);
                    transition: all 0.5s ease;
                }
            }
            .price-content {
                .price-title {
                    .like-cls {
                        .effect {
                            opacity: 0.3;
                        }
                    }
                }
            }
        }
    }
    .slick-slider {
        .slick-list {
            margin-left: -15px;
            margin-right: -15px;
        }
        .slick-slide {
            margin: 0 15px;
        }
    }
}

.flight-detail {
    background-image: url(../images/flights/bg.jpg);
