.book-table {
    position: relative;
    background-image: url("../images/restaurant/table-bg.png");
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center;
    .table-form {
        background-color: white;
        border-radius: 25px;
        padding: 40px;
        margin-bottom: 40px;
        position: relative;
        z-index: 1;
        form {
            display: flex;
            align-items: center;
            .row {
                margin: 0;
            }
            .form-group {
                margin-bottom: 0;
                margin-right: 40px;
                position: relative;
                input {
                    background-color: $inner-bg;
                    border: none;
                    border-radius: 35px;
                    padding: 20px 20px 18px;
                    line-height: 1;
                    text-transform: capitalize;
                    font-weight: 700;
                }
                img {
                    position: absolute;
                    right: 20px;
                    top: 20px;
                }
            }
            .form-control {
                background-color: $inner-bg;
                border: none;
                border-radius: 35px;
                padding: 20px 20px 18px;
                line-height: 1;
                text-transform: capitalize;
                font-weight: 700;
                &:focus {
                    z-index: 1;
                    box-shadow: none;
                }
            }
            .input-group-append {
                position: absolute;
                right: 12px;
                top: 12px;
                .btn {
                    border: none;
                    background-color: transparent;
                    padding: 0;
                    i {
                        color: rgba(#c5c5c5, 0.6);
                    }
                }
            }
            .gj-datepicker {
                margin-right: 40px;
                width: auto;
            }
            .btn {
                text-transform: capitalize;
                font-weight: 800;
                margin-left: 15px;
            }
            ::placeholder {
                color: #c5c5c5;
                font-weight: 700;
                font-size: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
            }
        }
        &:after {
            content: "";
            position: absolute;
            width: calc(100% + 12px);
            border-radius: 24px;
            background-color: rgba(white, 0.2);
            height: 100%;
            left: -6px;
            top: 0;
            z-index: -1;
        }
        &:before {
            content: "";
            position: absolute;
            width: calc(100% + 24px);
            border-radius: 24px;
            background-color: rgba(white, 0.1);
            height: 100%;
            left: -12px;
            top: 0;
            z-index: -1;
        }
    }
    .detail {
        font-size: calc(16px + (26 - 16) * ((100vw - 320px) / (1920 - 320)));
        font-family: $font_6, $font_4;
        color: white;
        margin-bottom: 0;
        margin-top: 0;
        text-align: center;
    }
    &.single-table {
        background-image: none;
        overflow: visible;
        .table-form {
            margin-bottom: 0;
            box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11);
            margin-top: -75px;
            &.classic-form {
                border-radius: 0;
                padding: 25px;
                margin-top: 0;
                box-shadow: none;
                form {
                    .form-group {
                        input {
                            border-radius: 0;
                            padding: 15px 15px 13px 25px;
                        }
                        img {
                            right: 15px;
                            top: 15px;
                        }
                    }
                    .form-control {
                        border-radius: 0;
                        padding: 15px 15px 13px;
                    }
                    .input-group-append {
                        .btn {
                            i {
                                top: 4px;
                                left: 15px;
                            }
                        }
                    }
                    .btn-rounded {
                        border-radius: 0;
                        margin-left: 0;
                    }
                }
                &:after,
                &:before {
                    display: none;
                }
            }
        }
    }
    &.input-radius-cls {
        .table-form {
            &.classic-form {
                form {
                    .form-group {
                        input {
                            background-color: rgba($theme-color1, 0.1);
                            border-radius: 25px;
                        }
                    }
                    .gj-datepicker {
                        margin-right: 0;
                    }
                    .btn-rounded {
                        border-radius: 100px;
                    }
                }
            }
        }
    }
}

