        font-size: 36px;
        font-weight: bold;
        position: absolute;
        right: 25%;
        width: auto;
        top: 50%;
        display: block;
        transform: translateY(-50%);
        text-align: center;
    }
    .flip {
        height: 50px;
        overflow: hidden;
        >div {
            >div {
                color: white;
                padding: 4px 12px;
                height: 40px;
                margin-bottom: 45px;
                display: inline-block;
                line-height: 1;
            }
        }
        div {
            &:first-child {
                animation: show 5s linear infinite;
                div {
                    background: #4ac6da;
                }
            }
            &:last-child {
                div {
                    background: #dc143c;
                }
            }
            div {
                background: #f4c789;
            }
        }
    }
    @keyframes show {
        0% {
            margin-top: -270px;
        }
        5% {
            margin-top: -180px;
        }
        33% {
            margin-top: -180px;
        }
        38% {
            margin-top: -90px;
        }
        66% {
            margin-top: -90px;
        }
        71% {
            margin-top: 0;
        }
        99.99% {
            margin-top: 0;
        }
        100% {
            margin-top: -270px;
        }
    }
}

