// team section
.team-section {
    .team-box {
        position: relative;
        .img-part {
            margin-bottom: 30px;
            img {
                box-shadow: 5px 8.66px 20px 0px rgba(0, 0, 0, 0.15);
                width: 100%;
            }
        }
        .team-content {
            min-width: 240px;
            padding: 20px;
            background-color: white;
            position: absolute;
            bottom: -30px;
            left: 50%;
            transform: translateX(-50%);
            text-align: center;
            h3 {
                font-size: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
                color: black;
                text-transform: capitalize;
                margin-bottom: 5px;
                margin-top: -4px;
            }
            h6 {
                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                color: $theme-color1;
                text-transform: capitalize;
                font-weight: 800;
                margin-bottom: -2px;
            }
        }
        .team-social {
            position: absolute;
            bottom: 80px;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            .social-box {
                width: 45px;
                height: 45px;
                background-color: white;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 8px;
                opacity: 0;
                transform: scale(0.8);
                transition: all 0.5s ease;
            }
        }
        &:hover {
            .team-social {
                .social-box {
                    opacity: 1;
                    transform: scale(1);
                    transition: all 0.5s ease;
                }
            }
        }
    }
    .team-slider {
        margin-bottom: 0;
    }
    .slick-slide {
        >div {
            margin: 0 25px;
        }
    }
    .slick-list {
        margin-left: -25px;
        margin-right: -25px;
    }
    .slick-dots {
        padding-top: 45px;
        position: relative;
        bottom: 0;
        li {
            z-index: 1;
            button {
                &:before {
                    color: $theme-color1;
                    font-size: 12px;
                }
            }
            &.slick-active {
                button {
                    &:after {
                        position: absolute;
                        top: 0;
                        left: -2px;
                        content: "";
                        width: 20px;
                        height: 20px;
                        font-family: "slick";
                        font-size: 24px;
                        line-height: 20px;
                        text-align: center;
                        color: $theme-color1;
                        opacity: 0.1;
                        z-index: -1;
                    }
                }
            }
        }
    }
}

// facts section
.facts-section {
    .facts-box {
        background-color: white;
        padding: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        box-shadow: 2px 3px 13px 0px rgba(0, 0, 0, 0.06);
        .img {
            margin-bottom: 15px;
            img {
                transform: scale(1);
                transition: all 0.5s ease;
            }
        }
        h3 {
            font-size: calc(20px + (40 - 20) * ((100vw - 320px) / (1920 - 320)));
            font-weight: 700;
            margin-bottom: 0;
        }
        h6 {
            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
            color: $theme-color1;
            margin-bottom: 0;
            font-weight: 700;
            text-transform: capitalize;
            margin-bottom: -2px;
        }
        &:hover {
            .img {
                img {
                    transform: scale(1.1);
                    transition: all 0.5s ease;
                }
            }
        }
    }
}

// category page //
.filter-panel {
    background-color: white;
    padding: 25px;
    display: flex;
    align-items: center;
    position: relative;
    .popup-btn {
        display: flex;
        align-items: center;
        h5 {
            font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
            text-transform: capitalize;
            color: black;
            font-weight: 700;
            margin-bottom: 0;
        }
        img {
            margin-left: 10px;
        }
    }
    .respon-filter-btn {
        display: none;
    }
    .left-filter {
        display: inline-block;
        .filters {
            ul {
                h4 {
                    margin-bottom: 0;
                    line-height: 1;
                }
                li {
                    padding: 7px 17px;
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                    // background-color: #fdfdfd;
                    cursor: pointer;
                    background-color: $inner-bg;
                    transition: all 0.5s ease;
                    font-weight: 700;
                    margin-right: 10px;
                    text-transform: capitalize;
                    &.active {
                        background-color: $theme-color1;
                        color: white;
                        transition: all 0.5s ease;
                    }
                }
            }
        }
    }
    .collection-grid-view {
        display: inline-block;
        margin: 0 auto;
        padding-left: 15px;
        ul {
            display: flex;
            align-items: center;
        }
        >ul {
            >li {
                margin: 0 4px;
            }
        }
        .filter-select {
            li {
                width: 6px;
                height: 17px;
                background-color: $theme-color1;
                margin-left: 4px;
                margin-bottom: -1px;
            }
        }
    }
    .right-panel {
        display: flex;
        align-items: center;
        margin-left: auto;
        ul {
            li {
                +li {
                    margin-left: 8px;
                }
            }
        }
    }
    &.bg-inner {
        background-color: $inner-bg !important;
        .left-filter {
            .filters {
                ul {
                    li {
                        background-color: white;
                        &.active {
                            background-color: $theme-color1;
                        }
                    }
                }
            }
        }
    }
    &.filter-title-bar {
        padding: 0;
        h4 {
            margin-bottom: 0;
            font-weight: 600;
        }
        .left-filter {
            .filters {
                ul {
                    margin-bottom: -15px;
                    li {
                        margin-bottom: 15px;
                    }
                }
            }
        }
    }
}
.custom-checkbox .form-check-input:checked ~ .form-check-label::before{
    background-color: $theme-color1;
}
.left-sidebar {
    background-color: white;
    margin-top: 30px;
    transition: all 0.5s ease;
    .back-btn {
        padding: 20px 25px;
        border-bottom: 1px solid $inner-bg;
        display: none;
        font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
        text-transform: capitalize;
        color: black;
        font-weight: 700;
        margin-bottom: 0;
        cursor: pointer;
    }
    .search-bar {
        position: relative;
        padding: 35px;
        border-bottom: 1px solid $inner-bg;
        input {
            width: 100%;
            border: none;
            background-color: $inner-bg;
            padding: 14px 14px 14px 50px;
        }
        i {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 50px;
            color: $theme-color1;
            &:after {
                content: "";
                position: absolute;
                width: 1px;
                height: 12px;
                background-color: rgba($theme-color1, 0.5);
                right: -10px;
                top: 2px;
            }
        }
        ::placeholder {
            font-size: 14px;
            color: black;
        }
    }
    .middle-part {
        padding: 25px 35px;
        .section-title {
            display: flex;
            align-items: center;
            padding-bottom: 20px;
            border-bottom: 1px solid $inner-bg;
            h5 {
                font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                text-transform: capitalize;
                color: black;
                font-weight: 700;
                margin-bottom: 0;
            }
            img {
                margin-left: auto;
            }
        }
        .filter-block {
            .collapse-block-title {
                position: relative;
                font-size: 14px;
                color: black;
                text-transform: capitalize;
                margin: 20px 0;
                cursor: pointer;
                font-weight: 600;
                &:after {
                    position: absolute;
                    display: block;
                    top: 0;
                    right: 0;
                    content: "+";
                    color: black;
                    padding: 0 6px;
                    background-color: $inner-bg;
                }
            }
            .collection-brand-filter {
                .collection-filter-checkbox {
                    margin-bottom: 6px;
                    input {
                        &:focus {
                            outline: none;
                            box-shadow: none;
                        }
                    }
                }
            }
            .sidebar-hotels {
                max-height: 210px;
                overflow-y: auto;
                .hotel-box {
                    display: flex;
                    align-items: center;
                    .img-left {
                        width: 100px;
                        height: auto;
                    }
                    .content-right {
                        padding-left: 20px;
                        h5 {
                            color: black;
                            font-weight: 700;
                            text-transform: capitalize;
                            margin-bottom: 5px;
                            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                        }
                        .rating {
                            margin-bottom: 5px;
                        }
                        .price {
                            font-weight: 700;
                            color: rgba(black, 0.6);
                            span {
                                color: rgba(black, 0.6);
                            }
                        }
                    }
                    +.hotel-box {
                        margin-top: 10px;
                    }
                }
            }
            .form-check-input {
                position: absolute;
                z-index: -1;
                opacity: 0;
                &:checked {
                    ~.form-check-label {
                        &:after {
                            background-image: url("../images/check-mark.svg");
                        }
                        &:before {
                            background-color: $theme-color1;
                        }
                    }
                }
            }
            .form-check-label {
                cursor: pointer;
                margin-left: 5px;
                font-size: 14px;
                color: rgba(black, 0.7);
                text-transform: capitalize;
                position: relative;
                .rating {
                    margin-top: 3px;
                }
                &:before {
                    content: "";
                    top: -2px;
                    height: 20px;
                    width: 20px;   
                    background: #f1f1f1;                
                    border: none;
                    left: -29px;
                    border-radius: 0;
                    position: absolute;
                    display: block;
                    user-select: none;
                    pointer-events: none;
                }
                &:after {
                    content: "";
                    top: -2px;
                    height: 20px;
                    width: 20px;
                    background-size: 48%;
                    left: -29px;
                    position: absolute;
                    display: block;
                    background-repeat: no-repeat;
                    background-position: center center;
                }
            }
            .collection-collapse-block {
                border-bottom: 1px solid $inner-bg;
                &.open {
                    .collapse-block-title {
                        &:after {
                            content: "-";
                        }
                    }
                }
                .collection-collapse-block-content {
                    padding-bottom: 12px;
                }
            }
        }
    }
    .bottom-info {
        padding: 0 35px 35px;
        h6 {
            margin-bottom: 0;
            font-size: 12px;
            color: rgba(black, 0.7);
            font-weight: 700;
        }
        h4 {
            font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
            font-weight: 700;
        }
        h5 {
            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
            background-color: $theme-color1;
            color: white;
            display: inline-block;
            text-transform: capitalize;
            font-weight: 700;
            padding: 6px 10px;
            border-radius: 50px;
            margin-bottom: 12px;
            span {
                text-transform: lowercase;
                font-size: 12px;
                border: 2px solid white;
                border-radius: 100%;
                padding: 0 6px;
            }
        }
    }
    &.sidebar-popup {
        display: none;
        position: absolute;
        z-index: 9;
        top: 80px;
        left: 0;
        border: 1px solid #f1f5f4;
        box-shadow: 0 0 5px #ddd;
        width: 280px;
        max-height: 530px;
        overflow-y: auto;
        &.open {
            display: block;
        }
    }
    &.sidebar-class {
        position: fixed;
        width: 300px;
        left: -300px;
        top: 0;
        margin-top: 0;
        z-index: 5;
        overflow-y: auto;
        height: 100vh;
        .back-btn {
            display: block;
        }
    }
}

