.mobile-filter {
    align-items: center;
    background-color: white;
    padding: 15px;
    display: none;
    h5 {
        font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
        text-transform: capitalize;
        color: black;
        font-weight: 700;
        margin-bottom: -3px;
    }
    img {
        margin-left: auto;
    }
}

.map-section {
    margin-top: 30px;
    z-index: 1;
    position: relative;
    iframe,
    .map {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}

.infoBox {
    .marker-detail {
        background-color: white;
        img {
            width: 180px;
        }
        .detail-part {
            padding: 6px;
            h6 {
                font-weight: 600;
                margin-bottom: 5px;
                text-transform: capitalize;
                color: black;
            }
            .rating {
                margin-bottom: 3px;
                &.one-star {
                    i {
                        &:first-child {
                            font-weight: 900;
                        }
                    }
                }
                &.two-star {
                    i {
                        &:nth-child(-n+2) {
                            font-weight: 900;
                        }
                    }
                }
                &.three-star {
                    i {
                        &:nth-child(-n+3) {
                            font-weight: 900;
                        }
                    }
                }
                &.four-star {
                    i {
                        &:nth-child(-n+4) {
                            font-weight: 900;
                        }
                    }
                }
                &.five-star {
                    i {
                        font-weight: 900;
                    }
                }
            }
            span {
                display: block;
                font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (1920 - 320)));
                font-weight: 400;
                color: #3e3e3e;
            }
            a {
                position: absolute;
                right: 7px;
                bottom: 7px;
                font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (1920 - 320)));
                color: $theme-color1;
            }
        }
    }
}

.view-map {
    text-transform: capitalize;
    color: black;
    margin-right: 15px;
    font-weight: 700;
    transition: all 0.5s ease;
    position: relative;
    &:hover {
        color: $theme-color1;
        transition: all 0.5s ease;
    }
    &.mobile-map {
        display: none;
    }
    .arrow {
        position: absolute;
        width: 100%;
        top: -58px;
        font-weight: 600;
        font-size: 14px;
        color: black;
        cursor: auto;
        &:after {
            content: "";
            background-image: url("../images/icon/arrow.png");
            width: 50px;
            height: 50px;
            position: absolute;
            background-size: contain;
            transform: rotate(115deg);
            left: -70px;
            bottom: -30px;
            background-repeat: no-repeat;
        }
        &:hover {
            color: black;
        }
    }
}

.onclick-map {
    display: none;
    iframe,
    .map {
        width: 100%;
        height: 300px;
        margin-top: 30px;
    }
    >div {
        margin-top: 30px;
    }
    &.show {
        display: block;
    }
}

.map-modal {
    .btn-close {
        position: absolute;
        top: 1px;
        right: 10px;
        z-index: 2;
        font-size: 1.9rem;
        background: none;
    }
    .modal-dialog {
        width: 100%;
        height: 90%;
        max-width: 97%;
        .modal-content {
            border: 0 none;
            border-radius: 0;
            background-color: $inner-bg;
            .modal-body {
                display: flex;
                align-items: center;
                .left-sidebar {
                    height: 100%;
                    overflow: scroll;
                }
            }
            iframe,
            .map {
                width: 100%;
                height: 100%;
            }
        }
    }
}

.list-view {
    margin-top: 30px;
    .list-box {
        display: flex;
        padding: 15px;
        background-color: white;
        .list-img {
            width: 35%;
        }
        .list-content {
            margin-left: 25px;
            display: flex;
            align-items: center;
            width: 60%;
            >div {
                width: 100%;
                position: relative;
            }
            h5 {
                font-size: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
                text-transform: capitalize;
                margin-bottom: 0;
                color: black;
                font-weight: 800;
                line-height: 1.5;
            }
            h6 {
                color: #797979;
            }
            p {
                color: rgba(0, 0, 0, 0.7);
                text-transform: capitalize;
                font-weight: 400;
            }
            .price {
                color: $grey-3;
                font-size: calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));
                font-weight: 800;
                margin-top: 15px;
                del {
                    font-size: 14px;
                    color: rgba(black, 0.6);
                    font-weight: 700;
                    margin-right: 10px;
                }
                span {
                    font-size: 14px;
                }
            }
            .rating {
                span {
                    color: #3e3e3e;
                    font-size: 12px;
                    font-weight: 700;
                    margin-left: 18px;
                }
            }
            .facility-icon {
                display: flex;
                text-align: center;
                margin-top: 20px;
                align-items: center;
                .facility-box {
                    span {
                        text-transform: capitalize;
                        color: #5f5f5f;
                        font-size: 12px;
                    }
                    img {
                        display: block;
                        width: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));
                        margin: 0 auto;
                    }
                    i {
                        display: block;
                        font-size: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
                        color: rgba(#5f5f5f, 0.64);
                    }
                    +.facility-box {
                        margin-left: 15px;
                    }
                }
            }
            .hotel-info {
                margin-top: 12px;
            }
            .book-now {
                position: absolute;
                right: 0;
                bottom: 0;
            }
            .offer-box {
                position: absolute;
                right: 0;
                top: 0;
                border: 1px solid $inner-bg;
                padding: 10px;
                color: $theme-color1;
                font-weight: 700;
                max-width: 150px;
            }
        }
        +.list-box {
            margin-top: 30px;
        }
        .slick-prev {
            left: 1px;
            z-index: 1;
            transition: all 0.5s ease;
            background-color: transparent;
            height: 36px;
            &:before {
                content: "\f053";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
            }
        }
        .slick-next {
            right: 0;
            z-index: 1;
            transition: all 0.5s ease;
            background-color: transparent;
            height: 36px;
            &:before {
                content: "\f054";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
            }
        }
        .slider-nav {
            margin-top: 3px;
            .slick-slide {
                margin: 0 3px;
                cursor: pointer;
            }
            .slick-list {
                margin-left: -3px;
                margin-right: -3px;
            }
        }
        &:hover {
            .special-img {
                .bg-size {
                    transform: scale(1);
                }
            }
            .slick-next,
            .slick-prev {
                background-color: #252525;
                transition: all 0.5s ease;
            }
        }
    }
}

.pagination-section {
    margin-top: 40px;
    .pagination {
        justify-content: center;
        .page-item {
            &.active {
                .page-link {
                    background-color: $theme-color1;
                    color: white;
                }
            }
            .page-link {
                color: black;
                background-color: white;
                border: none;
                margin: 0 3px;
                box-shadow: 0 0 8px #dadada;
                &:focus {
                    box-shadow: none;
                }
            }
        }
    }
}

.filter_button {
    align-items: center;
    background-color: white;
    display: flex;
    h5 {
        font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
        text-transform: capitalize;
        color: black;
        font-weight: 700;
        margin-bottom: -3px;
    }
    img {
        margin-right: 10px;
    }
}

.filter-bottom-content {
    display: none;
    position: absolute;
    left: 0;
    top: 80px;
    width: 100%;
    z-index: 2;
    transition: all 0.5s ease;
    box-shadow: 0px 1px 9px 3px rgba(228, 228, 228, 0.81);
    transition-property: all;
    transition-duration: 0.5s;
    transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    .middle-part {
        .filter-block {
            .collapse-block-title {
                cursor: default;
                margin-top: 0;
                font-weight: 700;
                &:after {
                    display: none;
                }
            }
            .collection-collapse-block {
                border-bottom: none;
                .collection-collapse-block-content {
                    padding-bottom: 0;
                }
            }
            .collection-brand-filter {
                .collection-filter-checkbox {
                    margin-bottom: 10px;
                }
            }
        }
        .button_bottom {
            width: 100%;
        }
        .btn {
            text-transform: capitalize;
            padding: 5px 15px;
        }
    }
}

// single page
.hotel-single-section {
    background-position: center;
    background-size: cover;
    height: 450px;
    position: relative;
    overflow: hidden;
    .hotel-title-section {
        background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(231, 56, 39, 0) 100%);
        position: absolute;
        bottom: 0;
        width: 100%;
        .hotel-name {
            padding: 40px 0;
            display: flex;
            .left-part {
                .top {
                    display: flex;
                    align-items: center;
                    margin-bottom: 18px;
                    h2 {
                        color: white;
                        margin-bottom: 0;
                        font-size: calc(16px + (36 - 16) * ((100vw - 320px) / (1920 - 320)));
                    }
                    .rating {
                        margin-left: 15px;
                    }
                    .share-buttons {
                        .btn {
                            padding: 4px 10px;
                            text-transform: capitalize;
                            border-radius: 5px;
                            margin-left: calc(5px + (10 - 5) * ((100vw - 320px) / (1920 - 320)));
                            color: black;
                            background-color: $inner-bg;
                        }
                    }
                }
                p {
                    color: rgba(white, 0.9);
                    margin-bottom: 0;
                    font-weight: 700;
                }
                .facility-detail {
                    margin-top: 20px;
                    margin-bottom: -6px;
                    span {
                        color: white;
                        font-weight: 600;
                        font-size: 14px;
                        text-transform: capitalize;
                        padding: 2px 8px;
                        border: 1px solid #ddd;
                        border-radius: 5px;
                        i {
                            margin-right: 3px;
                        }
                        +span {
                            margin-left: 10px;
                        }
                    }
                }
            }
            .right-part {
                margin-left: auto;
                text-align: right;
                .price {
                    font-size: calc(16px + (36 - 16) * ((100vw - 320px) / (1920 - 320)));
                    color: white;
                    span {
                        font-size: 16px;
                    }
                }
                .btn {
                    padding: 7px 13px;
                }
            }
        }
    }
}

