.single-section {
    position: relative;
    .image_section {
        position: relative;
        .view-all {
            position: absolute;
            width: 100%;
            bottom: 0;
            background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(231, 56, 39, 0) 100%);
            margin-bottom: 0;
            padding: 10px;
            text-transform: capitalize;
            color: white;
            font-weight: 800;
            line-height: 1;
        }
        .slider-thumbnail {
            margin-top: 5px;
            overflow: hidden;
            .slick-slide {
                >div {
                    margin: 0 5px;
                }
            }
            .slick-list {
                margin-left: -5px;
                margin-right: -5px;
            }
        }
    }
    .facility_sec {
        display: flex;
        justify-content: center;
        background-color: white;
        margin-top: 20px;
        .icon-box {
            display: flex;
            align-items: center;
            padding: 0 50px;
            position: relative;
            svg {
                width: 30px;
                height: auto;
                fill: $theme-color1;
            }
            .content {
                margin-left: 15px;
                h5 {
                    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
                    text-transform: capitalize;
                    font-weight: 700;
                    margin-bottom: 0;
                }
                h6 {
                    font-size: 12px;
                    color: rgba(black, 0.7);
                    text-transform: capitalize;
                    margin-bottom: 0;
                }
            }
            &:after {
                content: "";
                width: 1px;
                height: 25px;
                background-color: $theme-color1;
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
    }
    .description-section {
        .menu-top {
            margin-top: 30px;
            padding: 12px 0;
            background-color: white;
            box-shadow: 0 2px 4px 0 $box-shadow;
            li {
                a {
                    color: black;
                    text-transform: uppercase;
                    font-weight: 700;
                    padding: 17px 30px;
                    transition: all 0.5s ease;
                }
                &.active {
                    a {
                        color: $theme-color1;
                        border-bottom: 2px solid $theme-color1;
                        transition: all 0.5s ease;
                    }
                }
                &.mobile-menu {
                    display: none;
                }
            }
            &.sticky {
                position: fixed;
                z-index: 3;
                top: 0;
                left: 0;
                width: 100%;
                box-shadow: 1px 3px 20px 0px rgba(0, 0, 0, 0.1);
                margin-top: 0;
                padding: 16px 0;
                animation: smoothScroll 1s forwards;
                li {
                    a {
                        padding: 15px 30px;
                    }
                }
            }
            &.menu-up {
                margin-top: 0;
                margin-bottom: 30px;
            }
        }
        .description-details {
            .content-title {
                text-transform: uppercase;
                font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
                font-weight: 700;
            }
            .menu-part {
                margin-top: 20px;
                padding: 30px;
                background-color: white;
                margin-bottom: 30px;
                box-shadow: 0 2px 4px 0 $box-shadow;
                &.page-section {
                    .zoom-gallery {
                        >div {
                            &:nth-child(-n+3) {
                                margin-bottom: 0;
                            }
                        }
                    }
                }
                .accordion-plan {
                    position: relative;
                    .card {
                        border-radius: 0;
                        border: 1px solid $inner-bg;
                        .card-header {
                            background-color: $inner-bg;
                            border-bottom: 0;
                            padding: 6px;
                            button {
                                display: block;
                                width: 100%;
                                text-align: left;
                                color: #353535;
                                text-transform: capitalize;
                                &:before {
                                    content: "";
                                    position: absolute;
                                    width: 0;
                                    height: 0;
                                    border-left: 7px solid transparent;
                                    border-right: 7px solid transparent;
                                    border-top: 6px solid #353535;
                                    right: 20px;
                                    top: 20px;
                                    transition: all 0.3s ease;
                                }
                            }
                            button[aria-expanded="true"] {
                                &:before {
                                    border-top: 0;
                                    border-bottom: 6px solid #353535;
                                    transition: all 0.3s ease;
                                }
                            }
                        }
                        .card-body {
                            color: #444444;
                            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                            .highlight {
                                margin-top: 10px;
                                li {
                                    display: block;
                                    text-transform: capitalize;
                                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                                    i {
                                        margin-right: 5px;
                                        font-size: 12px;
                                    }
                                    img {
                                        margin-right: 5px;
                                        width: 18px;
                                    }
                                }
                            }
                        }
                        +.card {
                            margin-top: 20px;
                        }
                    }
                }
                .list-view {
                    margin-top: 0;
                    .list-img{
                        width: 30%;
                    }
                    .facility-icon{
                        margin-top: 10px;
                    }
                    .list-box {
                        padding: 0;
                    }
                    +.list-view {
                        margin-top: 20px;
                    }
                }
                .zoom-gallery {
                    .overlay {
                        position: relative;
                        .overlay-background {
                            position: absolute;
                            left: 0;
                            top: 0;
                            width: 100%;
                            height: 100%;
                            z-index: 1;
                            transition: all 0.4s ease;
                            i {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -50%);
                                color: white;
                                font-size: 22px;
                                display: none;
                                transition: all 0.4s ease;
                                align-items: center;
                                justify-content: center;
                            }
                        }
                        &:hover {
                            .overlay-background {
                                transition: all 0.4s ease;
                                background-color: rgba(black, 0.6);
                                i {
                                    display: flex;
                                    transition: all 0.4s ease;
                                }
                            }
                        }
                    }
                    >div {
                        &:nth-child(-n+3) {
                            margin-bottom: 30px;
                        }
                    }
                }
                &.facility {
                    h6 {
                        text-transform: capitalize;
                        font-weight: 700;
                        color: $grey-darker;
                        img {
                            margin-right: 5px;
                            width: 20px;
                        }
                    }
                    ul {
                        margin-left: 10px;
                        li {
                            display: block;
                            text-transform: capitalize;
                            line-height: 1.9;
                            i {
                                font-size: 8px;
                                margin-right: 5px;
                                color: rgba(0, 0, 0, 0.6);
                            }
                        }
                    }
                }
                &.about {
                    h6 {
                        font-weight: 700;
                        text-transform: capitalize;
                    }
                    p {
                        color: #383838;
                        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                        line-height: 1.6;
                    }
                    .about-sec {
                        h4 {
                            font-weight: 700;
                            margin-bottom: 10px;
                        }
                        h6 {
                            color: black;
                        }
                        p {
                            color: rgba(black, 0.7);
                            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                            &.top-space {
                                margin-top: -6px;
                            }
                            &.bottom-space {
                                margin-bottom: -7px;
                            }
                        }
                        ul {
                            padding-left: 25px;
                            li {
                                list-style-type: square;
                                display: list-item;
                                text-transform: capitalize;
                                color: rgba(black, 0.7);
                                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                                line-height: 1.7;
                                letter-spacing: 0.04em;
                            }
                        }
                        +.about-sec {
                            margin-top: 15px;
                        }
                        .city-detail {
                            margin-bottom: 20px;
                            font-size: 18px;
                            color: #353535;
                        }
                    }
                    .detail-img {
                        margin: 20px 0;
                    }
                }
                &.map {
                    iframe {
                        width: 100%;
                        height: 420px;
                        margin-bottom: -5px;
                    }
                }
                &.review {
                    .review-box {
                        .rating {
                            span {
                                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                                margin-left: 10px;
                                font-weight: 700;
                            }
                        }
                        h6 {
                            font-size: 14px;
                            text-transform: capitalize;
                            color: rgba(0, 0, 0, 0.5);
                            margin-top: 4px;
                        }
                        p {
                            color: #4a4a4a;
                            line-height: 1.6;
                            margin-bottom: 0;
                            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                        }
                        +.review-box {
                            border-top: 1px solid $inner-bg;
                            padding-top: 12px;
                            margin-top: 15px;
                        }
                    }
                }
                &.policy {
                    p {
                        color: #212121;
                        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                        line-height: 1.6;
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
                +.menu-part {
                    margin-top: 20px;
                }
                .rooms-box {
                    width: 100%;
                    tbody {
                        tr {
                            display: block;
                            +tr {
                                margin-top: 20px;
                                padding-top: 10px;
                                border-top: 1px solid $inner-bg;
                            }
                        }
                    }
                    td {
                        &:first-child {
                            width: 25%;
                        }
                        &:nth-child(2) {
                            width: 55%;
                        }
                        &:nth-child(3) {
                            width: 20%;
                            text-align: right;
                        }
                    }
                    .price-details {
                        display: flex;
                        align-items: center;
