                                border: 1px solid green;
                                top: 2px;
                            }
                            &:before {
                                content: "";
                                left: 3px;
                                width: 8px;
                                height: 8px;
                                background-color: green;
                                position: absolute;
                                border-radius: 100%;
                                top: 5px;
