        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100vh;
        padding-top: 47px;
        .description-section {
            width: 100%;
            .description-details {
                .menu-part {
                    margin-top: 0;
                    margin-bottom: 0;                    
                    background-color: rgba(255, 255, 255, 1);
                    position: relative;
                    h2{
                        color: #3c7492;
                    }
                    .accordion-plan {
                        margin-left: 0;
                        &:after {
                            display: none;
                        }
                        .card {
                            border: 0;
                            background-color: transparent;
                            .card-header {
                                background-color: transparent;
                                padding: 0;
                                border-bottom: 1px solid $inner-bg;
                                button {
                                    padding-left: 0;
                                    &:before {
                                        display: none;
                                    }
                                }
                            }
                            .card-body {
                                padding: 15px 0 0;
                                line-height: 1.4;
                                .highlight {
                                    li {
                                        display: flex;
                                        +li {
                                            margin-left: 5px;
                                        }
                                    }
                                }
                            }
                            +.card {
                                margin-top: 12px;
                            }
                        }
                    }
                    .list-view {
                        .list-box {
                            background-color: transparent;
                        }
                    }
                }
            }
        }
    }
    .full-slider {
        .slick-dots {
            bottom: 40px;
            z-index: 1;
            li {                
                text-transform: capitalize;
                font-size: 16px;
                font-weight: 500;
                color: white;
                width: auto;
                &.slick-active {
                    .pager__item{
                        background-color: #3c7492;
                        border-radius: 25px;
                    }                    
                }
                .pager__item{
                    padding: 10px 30px;
                    color: white;
                }
                button {
                    &:before {
                        font-size: 12px;
                        color: $theme-color1;
                    }
                }
            }
        }
        .slick-arrow {
            .next-slick-img,
            .prev-slick-img {
                width: 70px;
                height: 70px;
                border-radius: 100%;
                overflow: hidden;
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                transform: scale(0.7);
                transition: all 0.5s ease;
                &:hover {
                    transform: scale(1);
                    transition: all 0.5s ease;
                }
            }
        }
        .slick-prev {
            left: 10px;
            z-index: 1;
            &:before {
                display: none;
            }
        }
        .slick-next {
            right: 60px;
            z-index: 1;
            &:before {
                display: none;
            }
        }
    }
    .overlay {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100px;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
    }
