    animation: 1s linear infinite bounce-1;
}

.add-filter {
    filter: grayscale(1);
}

@keyframes bounce-1 {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-7px);
    }
    100% {
        transform: translateY(0);
    }
}

// coming soon //
.coming-soon {
    background-size: cover;
    height: 100vh;
    position: relative;
    z-index: 1;
    overflow: hidden;
    .coming-soon-detail {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100vh;
        width: 100%;
        >div {
            width: 100%;
        }
        .logo {
            margin-bottom: calc(14px + (25 - 14) * ((100vw - 320px) / (1920 - 320)));
        }
        h2 {
            color: white;
            font-size: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
            font-weight: 700;
            margin-bottom: calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)));
        }
        form {
            width: 50%;
            margin: 0 auto;
            label {
                color: white;
                font-size: 20px;
                text-transform: capitalize;
            }
            input {
                padding: 13px 25px;
                border-radius: 25px;
                font-size: 16px;
                &:focus {
                    box-shadow: none;
                    border: 1px solid #dddddd;
                }
            }
            .actions {
                margin-top: 20px;
                .btn{
                    padding: 14px 40px;
                    border-radius: 25px;            
                }
            }
        }
        .timer {
            margin-bottom: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
        }
    }
    .object {
        position: absolute;
        animation: sway 2.4s infinite;
        animation-timing-function: ease-in-out;
        transform-origin: top;
        left: calc(10% + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
        height: 5%;
        z-index: 999;
        text-transform: uppercase;
        .object-shape {
            width: calc(60px + (100 - 60) * ((100vw - 320px) / (1920 - 320)));
            height: calc(60px + (100 - 60) * ((100vw - 320px) / (1920 - 320)));
            border-radius: 50%;
            display: block;
            background-color: white;
            margin: 0 auto;
            position: relative;
            color: black;
            text-align: center;
            padding-top: calc(14px + (25 - 14) * ((100vw - 320px) / (1920 - 320)));
            font-weight: 800;
            box-sizing: border-box;
            font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (1920 - 320)));
            span {
                font-size: calc(12px + (22 - 12) * ((100vw - 320px) / (1920 - 320)));
                color: black;
            }
        }
        .object-rope {
            height: 100%;
            width: 5px;
            background-color: white;
            content: "";
            display: block;
            margin-left: 50%;
        }
    }
    &.travel {
        background-color: rgba($theme-color1, 0.05);
        svg {
            position: absolute;
            top: 0;
            width: 1920px;
            height: 1600px;
            display: flex;
            align-items: center;
            justify-content: center;
            left: 50%;
            transform: translateX(-50%);
        }
        .coming-soon-detail {
            position: absolute;
            align-items: flex-end;
            padding-bottom: 60px;
            z-index: 1;
            .logo {
                img {
                    filter: invert(1);
                }
            }
            h2 {
                color: black;
            }
            form {
                label {
                    color: black;
                }
            }
        }
    }
    &.ripple-effect {
        canvas {
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;
        }
    }
}

// typography page //
.typography_section {
    p {
        color: #777777;
    }
    .typography-box {
        margin-bottom: 30px;
        p {
            line-height: 22px;
        }
        .headings {
            border-bottom: 1px solid #f1f1f1;
            margin-bottom: 20px;
            background-color: rgba(#f7f7f7, 0.5);
            padding: 8px 12px;
            h3 {
                text-transform: capitalize;
                color: #2d2d2d;
                margin-bottom: 0;
                font-weight: 400;
                font-size: 20px;
            }
            span {
                color: #777777;
                code {
                    color: #777777;
                }
            }
        }
        .typo-content {
            &.heading_content {
                h1,
                h2,
                h3,
                h4,
                h5,
                h6 {
                    font-weight: 400;
                    color: black;
                    text-transform: uppercase;
                    line-height: 1;
                    margin-bottom: 10px;
                }
            }
            .sub-title {
                border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                padding-bottom: 5px;
                margin-bottom: 8px;
                font-size: 18px;
                color: $grey-darker;
                text-transform: capitalize;
            }
            &.input_button {
                input {
                    margin-bottom: 10px;
                }
                label {
                    font-size: 16px;
                    text-transform: capitalize;
                    padding-left: 5px;
                }
            }
            &.loader-typo {
                width: 100%;
                justify-content: center;
                display: flex;
                .pre-loader {
                    position: relative;
                    width: 50px;
                    height: 50px;
                    background-color: $theme-color1;
                    border-radius: 50%;
                    &:after {
                        content: "";
                        position: absolute;
                        border-radius: 50%;
                        top: 50%;
                        left: 50%;
                        border: 0 solid white;
                        transform: translate(-50%, -50%);
                        animation: loading 1000ms ease-out forwards infinite;
                        transition: all 0.3s ease;
                    }
                }
            }
            .footer-social {
                margin-top: 0;
                justify-content: center;
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    code {
        color: rgba($theme-color1, 0.6);
        background-color: #f7f7f7;
        padding: 3px;
        margin: 0 3px;
        border-radius: 2px;
    }
}

@keyframes sway {
    0% {
        transform: rotate(8deg);
    }
    50% {
        transform: rotate(-8deg);
    }
    100% {
        transform: rotate(8deg);
    }
}

// travel animation
@-webkit-keyframes rotate-right {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

@-webkit-keyframes rotate-left {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(-360deg);
    }
}

@-webkit-keyframes hover {
    0% {
        -webkit-transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(5%);
    }
    100% {
        -webkit-transform: translateY(0%);
    }
}

@-webkit-keyframes pull {
    0% {
        -webkit-transform: scaleY(1);
    }
    40% {
        -webkit-transform: scaleY(1.01);
    }
    60% {
        -webkit-transform: scaleY(0.99);
    }
    80% {
        -webkit-transform: scaleY(1.01);
    }
    100% {
        -webkit-transform: scaleY(0.99);
    }
    80% {
        -webkit-transform: scaleY(1.01);
    }
    100% {
        -webkit-transform: scaleY(1);
    }
}

