            }
            p {
                margin-bottom: 0;
                color: rgba(black, 0.8);
                text-transform: capitalize;
                letter-spacing: 0.08em;
            }
            .complete-profile {
                margin-top: calc(10px + (20 - 10) * ((100vw - 320px) / (1920 - 320)));
                .complete-box {
                    display: flex;
                    align-items: center;
                    background-color: white;
                    padding: 15px;
                    text-align: center;
                    justify-content: center;
                    border-radius: 5px;
                    box-shadow: 0 2px 4px 0 rgba($box-shadow, 0.22);
                    i {
                        margin-right: 12px;
                        color: green;
                        font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
                    }
                    h6 {
                        margin-bottom: 0;
                        text-transform: capitalize;
                        font-weight: 600;
                        font-size: 14px;
                    }
                    &.not-complete {
                        i {
                            color: red;
                        }
                    }
                }
            }
        }
        .counter-section {
            .counter-box {
                background-color: $inner-bg;
                padding: 15px;
                text-align: center;
                img {
                    width: 35px;
                    margin-bottom: 15px;
                }
                h3 {
                    margin-bottom: 0;
                    font-weight: 600;
                }
                h5 {
                    color: rgba(0, 0, 0, 0.61);
                    margin-bottom: -5px;
                    text-transform: capitalize;
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                    margin-top: 5px;
                }
            }
        }
        .dashboard-info {
            margin-top: 25px;
            position: relative;
            #chart,
            .chart-box {
                padding-top: 20px;
                padding-left: 10px;
                background: $inner-bg;
            }
            select.flat-select {
                appearance: none;
                background: #008ffb url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='60px' height='60px'><polyline fill='white' points='46.139,15.518 25.166,36.49 4.193,15.519'/></svg>") no-repeat scroll right 2px top 9px / 16px 16px;
                border: 0 none;
                border-radius: 3px;
                color: white;
                font-size: 16px;
                font-weight: bold;
                outline: 0 none;
                height: 33px;
                padding: 5px 20px 5px 10px;
                text-align: center;
                text-indent: 0.01px;
                text-overflow: "";
                text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
                transition: all 0.3s ease 0s;
                width: auto;
            }
            select.flat-select:focus,
            select.flat-select:hover {
                border: 0;
                outline: 0;
            }
            .apexcharts-canvas {
                margin: 0 auto;
            }
            path#apexcharts-radialbarTrack-0 {
                stroke: rgba(251, 96, 122, 0.08);
            }
            path#apexcharts-radialbarTrack-1 {
                stroke: rgba(85, 169, 248, 0.08);
            }
            path#apexcharts-radialbarTrack-2 {
                stroke: rgba(162, 100, 255, 0.08);
            }
            .detail-left {
                position: absolute;
                ul {
                    display: block;
                    li {
                        font-size: 12px;
                        display: flex;
                        align-items: center;
                        font-weight: 600;
                        span {
                            width: 14px;
                            height: 5px;
                            display: block;
                            border-radius: 5px;
                            margin-right: 5px;
                            &.completed {
                                background-color: #fa4962;
                            }
                            &.upcoming {
                                background-color: #379cf9;
                            }
                            &.cancelled {
                                background-color: #a264ff;
                            }
                        }
                    }
                }
            }
            .activity-box {
                padding: 20px 10px;
                background: $inner-bg;
                h6 {
                    text-transform: capitalize;
                    font-weight: 700;
                    margin-bottom: 15px;
                }
                ul {
                    li {
                        display: block;
                        background-color: white;
                        padding: 8px 15px;
                        text-transform: capitalize;
                        position: relative;
                        i {
                            margin-right: 6px;
                        }
                        span {
                            float: right;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            left: 0;
                            height: 100%;
                            width: 2px;
                            background-color: red;
                            top: 0;
                        }
                        &.blue-line {
                            &:after {
                                background-color: #03a9f4;
                            }
                        }
                        &.yellow-line {
                            &:after {
                                background-color: #ffc107;
                            }
                        }
                        +li {
                            margin-top: 8px;
                        }
                    }
                }
            }
        }
    }
}

