        padding: 0.375rem 0.75rem;
    }
    .modal-title {
        text-transform: capitalize;
        font-weight: 700;
    }
    .modal-body {
        label {
            text-transform: capitalize;
        }
    }
}
.modal{
    .modal-header{
        padding:20px 30px;
    }
    .modal-body{
        padding:20px 30px;
    }
    .modal-footer{
        padding:20px 30px;
    }
}

/*=====================
22. Portfolio css
==========================*/

.portfolio-section {
    .filter-button-group {
        text-align: center;
        ul {
            li {
                padding: 7px 17px;
                font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                cursor: pointer;
                transition: all 0.5s ease;
                font-weight: 700;
                margin: 0 10px;
                text-transform: capitalize;
                background-color: $inner-bg;
                &.active {
                    background-color: $theme-color1;
                    color: white;
                }
            }
        }
    }
    .grid {
        .grid-item {
            margin-top: 30px;
            .overlay {
                overflow: hidden;
                margin: -1px;
                transition: all 0.5s ease;
                .portfolio-image {
                    transition: all 0.5s ease;
                    cursor: pointer;
                }
                .portfolio-text {
                    margin-top: 10px;
                    h3 {
                        font-weight: 600;
                        margin-bottom: 5px;
                        text-transform: capitalize;
                        font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
                    }
                    h6 {
                        margin-bottom: 0;
                        text-transform: capitalize;
                        font-size: 14px;
                        color: grey;
                    }
                }
                &:hover {
                    .portfolio-image {
                        transform: scale(1.02);
                        transition: all 0.5s ease;
                    }
                }
            }
            a {
                cursor: zoom-in;
            }
        }
        &.blog-masonry {
            .grid-item {
                a {
                    cursor: pointer;
                }
                .overlay {
                    overflow: visible;
                    .blog-details {
                        padding-top: 20px;
                        h6 {
                            color: $grey-13;
                            text-transform: capitalize;
                            font-size: 14px;
                            font-weight: 700;
                            margin-bottom: 10px;
                            i {
                                color: rgba(35, 0, 0, 0.24);
                                margin-right: 12px;
                            }
                        }
                        h5 {
                            font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
                            color: $grey-3;
                            font-weight: 700;
                            line-height: 1.5;
                            margin-bottom: 5px;
                            transition: all 0.5s ease;
                        }
                        p {
                            font-size: 14px;
                            color: $grey-13;
                            line-height: 1.5;
                            margin-bottom: -3px;
                        }
                        .link {
                            margin-bottom: 0;
                            margin-top: 15px;
                            a {
                                color: rgba(0, 0, 0, 0.74);
                            }
                        }
                    }
                    &:hover {
                        .portfolio-image {
                            transform: scale(1);
                        }
                    }
                }
            }
        }
    }
    .isotopeSelector {
        float: left;
        width: 100%;
        position: relative;
        margin-top: 30px;
        img {
            width: 100%;
            height: auto;
        }
    }
    &.creative-section {
        .grid {
            .grid-item {
                margin-top: 0;
            }
        }
        .filter-button-group {
            margin-bottom: 40px;
        }
    }
}

.effect-cls {
    position: relative;
    &:after {
        content: "";
        position: absolute;
        background: url(../images/effect.png);
        bottom: -12px;
        width: 100%;
        height: 110px;
        left: 0;
        z-index: 1;
    }
    &.breadcrumb-section {
        height: calc(300px + (580 - 300) * ((100vw - 320px) / (1920 - 320)));
    }
}

.effect-cls-up {
    &:before {
        content: "";
        position: absolute;
        background: url(../images/effect.png);
        top: -1px;
        width: 100%;
        height: 85px;
        z-index: 1;
        background-repeat: no-repeat;
        transform: rotate(180deg);
    }
}

.parallax-section {
    .full_banner {
        height: 93vh;
        background-attachment: fixed;
    }
}

.center-slide {
    margin-bottom: -3px;
    .image-center {
        height: 93vh;
        .center-content {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 93vh;
            text-align: center;
            position: relative;
            z-index: 1;
            h3 {
                font-weight: 600;
                margin-bottom: 5px;
                text-transform: capitalize;
            }
            h6 {
                margin-bottom: 0;
                text-transform: capitalize;
            }
            &:after {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(white, 0.65);
                z-index: -1;
            }
        }
    }
    .slick-slide {
        .image-center {
            .center-content {
                opacity: 0;
                transition: all 0.5s ease;
            }
        }
        &.slick-center {
            .image-center {
                .center-content {
                    opacity: 1;
                    transition: all 0.5s ease;
                    h3 {
                        animation: fadeInLeft 0.4s ease;
                    }
                    h6 {
                        animation: fadeInRight 0.4s ease;
                        color: gray;
                        padding: 10px 20px;
                        background-color: #ffffff;
                    }
                }
            }
        }
    }
}

.portfolio-creative {
    &.odd-even-con{
        .container{
            .row{
                .content-sec{

                    text-align: left;
                    padding-left: 60px;
                    h3{
                        font-size: 200%;
                    }
                    p{
                        max-width: 80%;
                        margin: 0;
                        font-size: 16px;
                        line-height: 1.5;
                    }
                }
                &:nth-child(even){
                    .content-sec{
                        text-align: right;
                        padding-left: unset;
                        padding-right: 60px;
                    }
                    p{
                        margin-right: 0;
                        margin-left: auto;
                    }
                }
            }
        }
    }
    .content-sec {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        text-align: center;
        &.center-con{
            p{
                max-width: 80%;
                font-size: 16px;
                line-height: 1.4;    
            }                
            h3{
                font-size: 175%;
            }
        }
        h3 {
            text-transform: capitalize;
            font-weight: 700;
            position: relative;
        }
        p {
            color: #888888;
            max-width: 70%;
            margin: 0 auto;
        }
        .btn {
            text-transform: capitalize;
            padding: 6px 14px;
            margin-top: 25px;
        }
    }
}


/*=====================
23. Blog pages css
==========================*/

.blog-sidebar {
    .blog-wrapper {
        margin-bottom: 25px;
        .search-bar {
            position: relative;
            input {
                width: 100%;
                border: none;
                padding: 14px 14px 14px 50px;
                background-color: #f7f7f7;
            }
            i {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 14px;
                color: $theme-color1;
                &:after {
                    content: "";
                    position: absolute;
                    width: 1px;
                    height: 12px;
                    background-color: rgba($theme-color1, 0.5);
                    right: -10px;
                    top: 2px;
                }
            }
        }
        .sidebar-title {
            h5 {
                text-transform: capitalize;
                font-weight: 700;
                font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
                margin-bottom: 20px;
            }
        }
        .sidebar-content {
            .sidebar-list {
                li {
                    display: block;
                    padding: 10px;
                    border-bottom: 1px solid #eee;
                    position: relative;

                    a {
                        color: black;
                    }
                    i {
                        margin-right: 15px;
                        color: gray;
                    }
                    &:first-child {
                        padding-top: 0;
                    }
                }
            }
            .blog-post {

                li {
                    margin-bottom: 20px;
                    .media {
                        h6{
                            font-weight: 600;
                        }
                        img {
                            width: 100px;
                            margin-right: 15px;
                        }
                        .media-body {
                            p {
                                margin-bottom: 0;
                            }
                        }
                    }
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
            .tags {
                margin-top: -8px;
                li {
                    padding: 4px 10px;
                    background-color: rgba($inner-bg, 0.8);
                    margin: 8px 8px 0 0;
                    border-radius: 4px;
                    text-transform: capitalize;
                    a {
                        color: black;
                    }
                }
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.blog-inner {
    &.blog_section {
        overflow: visible;
        .blog-wrap {
            border-radius: 0;
            box-shadow: 1px 0px 4px 0px rgba(0, 0, 0, 0.1);
            .blog-image {
                .blog-label {
                    border-radius: 0;
                    padding: 8px 10px;
                    h3 {
                        font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
                    }
                    h6 {
                        font-size: 13px;
                    }
                }
            }
            .blog-details {
                padding: 20px;
                h6 {
                    margin-bottom: 5px;
                    i {
                        color: rgba(35, 0, 0, 0.24);
                    }
                }
                h5 {
                    margin-bottom: 10px;
                }
                .link {
                    margin-bottom: 0;
                    margin-top: 15px;
                    a {
                        color: rgba(0, 0, 0, 0.74);
                    }
                }
            }
        }
        .blog-list {
            .blog-wrap {
                display: flex;
                .blog-image {
                    width: 30%;
                }
                .blog-details {
                    width: 70%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    h6 {
                        i {
                            color: rgba(35, 0, 0, 0.24);
                        }
                    }
                }
            }
        }
    }
}

.blog-single-detail {
    .top-image {
        position: relative;
        .video {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            i {
                font-size: calc(25px + (40 - 25) * ((100vw - 300px) / (1920 - 300)));
                color: white;
                transition: all 0.5s ease;
            }
        }
        &:hover {
            .video {
                i {
                    color: $theme-color1;
                    transition: all 0.5s ease;
                }
            }
        }
    }
    .title-part {
        .post-detail {
            margin-top: calc(10px + (30 - 10) * ((100vw - 300px) / (1920 - 300)));
            li {
                font-weight: 600;
                color: #777777;
                +li {
                    padding-left: 15px;
                    margin-left: 15px;
                    border-left: 1px solid #ddd;
                }
            }
        }
        h3 {
            margin-bottom: calc(7px + (15 - 7) * ((100vw - 320px) / (1920 - 320)));
            margin-top: 5px;
            text-transform: capitalize;
            font-weight: 600;
            font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
        }
    }
    .detail-part {
        p {
            font-weight: 400;
            color: #777;
            margin-bottom: calc(10px + (25 - 10) * ((100vw - 300px) / (1920 - 300)));
            line-height: calc(20px + (24 - 20) * ((100vw - 300px) / (1920 - 300)));
            font-size: calc(13px + (15 - 13) * ((100vw - 300px) / (1920 - 300)));
        }
    }
    .comment-section {
        .comment {
            text-transform: capitalize;
            margin-bottom: 20px;
        }
        .comment-wrapper {
            .comment-box {
                .media {
                    img {
                        width: calc(60px + (70 - 60) * ((100vw - 320px) / (1920 - 320)));
                        border-radius: 100%;
                    }
                    .media-body {
                        margin-left: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));
                        .title {
                            display: flex;
                            padding-bottom: 10px;
                            margin-bottom: 10px;
                            border-bottom: 1px solid #dddddd;
                            .comment-user,
                            .comment-date {
                                display: flex;
                                h6 {
                                    color: #484848;
                                    text-transform: capitalize;
                                    font-weight: 500;
                                    margin-bottom: 0;
                                    font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (1920 - 320)));
                                }
                                i {
                                    color: rgba(0, 0, 0, 0.5);
                                    margin-right: 7px;
                                }
                            }
                            .comment-date {
                                margin-left: 15px;
                            }
                        }
                        .comment-detail {
                            p {
                                margin-bottom: 8px;
                                color: $grey-13;
                                line-height: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));
                            }
                        }
                        .reply-btn {
                            text-align: right;
                            a {
                                color: #555555;
                                text-transform: capitalize;
                            }
                        }
                    }
                    &.inner-comment {
                        margin-left: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
                        margin-top: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
                    }
                }
                +.comment-box {
                    margin-top: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
                }
            }
        }
    }
    .leave-comment {
        margin-top: 30px;
        .comment {
            text-transform: capitalize;
            margin-bottom: 20px;
        }
        .form-control {
            border: 0;
            border-radius: 0;
            background-color: #f7f7f7;
            font-size: 14px;
            line-height: 1.8;
            text-transform: capitalize;
            padding: 10px 18px;
        }
        .submit-btn {
            text-align: right;
            margin-top: 10px;
            .btn {
                text-transform: capitalize;
                padding: 5px 10px;
            }
        }
    }
}


/*=====================
24. Footer css
==========================*/

footer {
    .footer {
        background-color: $footer-bg;
        .footer-space {
            padding-left: 50px;
        }
        .footer-title {
            &.mobile-title {
                display: none;
            }
            h5 {
                color: white;
                text-transform: capitalize;
                margin-bottom: 60px;
                font-weight: 700;
                position: relative;
            }
            .according-menu {
                position: absolute;
                right: 15px;
                top: 2px;
                display: none;
            }
        }
        .footer-content {
            .contact-detail {
                .footer-logo {
                    margin-bottom: 35px;
                }
                p {
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                    color: $footer-color;
                    line-height: 1.4;
                    font-weight: 700;
                    display: block;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    margin-bottom: 40px;
                }
                .contact-list {
                    li {
                        color: $footer-color;
                        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                        margin-bottom: 12px;
                        text-transform: capitalize;
                        display: block;
                        i {
                            margin-right: 5px;
                            font-size: 12px;
                        }
                        &:last-child {
                            margin-bottom: -3px;
                        }
                    }
                }
            }
            .footer-links {
                ul {
                    li {
                        display: block;
                        margin-bottom: 7px;
                        a {
                            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                            text-transform: capitalize;
                            color: $footer-color;
                            font-weight: 600;
                            transition: all 0.5s ease;
                            &:hover {
                                color: white;
                                transition: all 0.5s ease;
                            }
                        }
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }
            .footer-map {
                iframe {
                    width: 330px;
                    height: 200px;
                }
            }
            .footer-blog {
                .media {
                    .img-part {
                        overflow: hidden;
                    }
                    .media-body {
                        margin-left: 25px;
                        h5 {
                            color: white;
                            text-transform: capitalize;
                            font-weight: 700;
                        }
                        p {
                            color: $footer-color;
                            font-weight: 600;
                            display: block;
                            display: -webkit-box;
                            -webkit-line-clamp: 3;
                            -webkit-box-orient: vertical;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            margin-bottom: 0;
                        }
                    }
                    +.media {
                        margin-top: 18px;
                    }
                }
            }
            .footer-place {
                .row {
                    >div {
                        &:nth-last-child(-n+3) {
                            margin-top: 25px;
                        }
                    }
                }
                .place {
                    overflow: hidden;
                    position: relative;
                    .overlay {
                        opacity: 0;
                        position: absolute;
                        height: 100%;
                        width: 100%;
                        top: 0;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: rgba(black, 0.4);
                        transform: scale(0);
                        transition: all 0.5s ease;
                        h6 {
                            color: white;
                            text-transform: capitalize;
                            font-weight: 700;
                        }
                    }
                    &:hover {
                        .overlay {
                            opacity: 1;
                            transform: scale(1);
                            transition: all 0.5s ease;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }
    .sub-footer {
        background-color: $footer-bottom;
        padding: 22px 0;
        .footer-social {
            ul {
                li {
                    margin-right: 35px;
                    a {
                        i {
                            color: $footer-social;
                            font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
                            transition: all 0.5s ease;
                        }
                        &:hover {
                            i {
                                color: rgba(white, 0.5);
                                transition: all 0.5s ease;
                            }
                        }
                    }
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
