/*=====================
25. Loader css
==========================*/

.loader-wrapper {
    height: 100vh;
    width: 100vw;
    background-color: white;
    display: block;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 12;
    top: 0;
    .loader {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0.5);
        width: 200px;
        height: 150px;
    }
    &.loader-text {
        .loader {
            width: auto;
            height: auto;
        }
    }
    &.inner-loader {
        position: absolute;
        height: 100%;
        width: 100%;
        background-color: $inner-bg;
        .loader {
            top: 25%;
            width: 100%;
            height: 100%;
            display: flex;
            margin-top: 9%;
            justify-content: center;
        }
    }
    &.food-loader {
        .loader {
            width: auto;
            height: auto;
        }
    }
    &.img-gif {
        display: flex;
        img {
            width: 20%;
        }
    }
}

.loader-section {
    position: relative;
    overflow: hidden;
}

.typewriter h1 {
    color: black;
    overflow: hidden;
    border-right: 0.15em solid $theme-color1;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: 0.15em;
    animation: typing 3.5s steps(30, end), blink-caret 0.5s step-end infinite;
}

@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

@keyframes blink-caret {
    from,
    to {
        border-color: transparent;
    }
    50% {
        border-color: $theme-color1;
    }
}

// flight loader
$c-loader-front: #cee4e7;
@mixin classPrefix($className) {
    [class^="#{$className}"],
    [class*=" #{$className}"] {
        @content;
    }
}

.animation {
    position: relative;
    width: 100%;
    height: 20rem;
    max-width: 55rem;
    background-color: $inner-bg;
    @include classPrefix("animation__") {
        position: absolute;
    }
    &__plane {
        position: absolute;
        z-index: 1;
        top: calc(40% - (62px / 2));
        left: calc(50% - (272px / 2));
        width: 17rem;
        height: 6.2rem;
        animation: 2s ease-in-out takeOff, 5s ease-in-out infinite alternate flight 2s;
        &--shadow {
            bottom: 1rem;
            left: calc(54% - (8rem / 2));
            width: 8rem;
            height: 1rem;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.15);
            animation: 2s ease-in-out takeOffShadow, 5s ease-in-out infinite alternate flightShadow 2s;
        }
    }
    &__cloud {
        &--front {
            z-index: 2;
            top: 50%;
            left: 20%;
            width: 8.5rem;
            height: 3rem;
            fill: $c-loader-front;
            animation: 4s linear infinite cloudFront;
        }
        &--middle {
            top: 22%;
            left: 75%;
            width: 6rem;
            height: 3rem;
            fill: $c-loader-front;
            animation: 5s linear infinite cloudMiddle;
        }
        &--back {
            top: 6%;
            left: 34%;
            fill: $c-loader-front;
            animation: 8s linear infinite cloudBack;
        }
    }
}

@keyframes loader {
    0% {
        left: -100%;
    }
    100% {
        left: 100%;
    }
}

@keyframes takeOff {
    0% {
        transform: translate(-220%, 110%);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes takeOffShadow {
    0% {
        transform: translate(-440%, 110%);
        opacity: 1;
    }
    100% {
        transform: translate(0, 0);
        opacity: 0.8;
    }
}

@keyframes flight {
    0% {
        transform: translate(0, 0);
    }
    25% {
        transform: translate(0, 10%);
    }
    75% {
        transform: translate(0, -10%);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes flightShadow {
    0% {
        transform: scale(0.8);
        opacity: 0.8;
    }
    25% {
        transform: scale(0.9);
        opacity: 1;
    }
    75% {
        transform: scale(1.1);
        opacity: 0.6;
    }
    100% {
        transform: scale(0.8);
        opacity: 0.8;
    }
}

@keyframes cloudFront {
    0% {
        transform: translate(520%, 0);
    }
    100% {
        transform: translate(-600%, 0);
    }
}

@keyframes cloudMiddle {
    0% {
        transform: translate(230%, 0);
    }
    100% {
        transform: translate(-900%, 0);
    }
}

@keyframes cloudBack {
    0% {
        transform: translate(910%, 0);
    }
    100% {
        transform: translate(-1000%, 0);
    }
}

%loader-dark {
    background: linear-gradient(90deg, rgba(212, 212, 212, 0.3) 8%, #e4e4e4 18%, rgba(212, 212, 212, 0.3) 33%);
    background-size: 800px 104px;
    animation: skeleton-loader 2s infinite linear;
}

