/*=====================
26.Tap to top CSS
==========================*/

.tap-top {
    width: 35px;
    height: 35px;
    border-radius: 8px;
    position: fixed;
    bottom: 50px;
    right: -60px;
    z-index: 8;
    color: white;
    text-align: center;
    background: $theme-color1;
    border: none;
    font-size: 22px;
    padding: 6px 5px 5px 5px;
    cursor: pointer;
    transform: rotate(-15deg);
    transition: all 0.5s ease;
    &:after {
        position: absolute;
        content: "";
        bottom: -12px;
        background-color: black;
        width: 100%;
        height: 11px;
        left: 0;
        transform: rotate(13deg);
        background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.15) 0%, rgba(0, 0, 0, 0) 100%);
    }
    >div {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &.top {
        display: block;
        right: 30px;
        transition: all 0.5s ease;
    }
}


/*=====================
27. Animation & effect css
==========================*/

.circle {
    border-radius: 50%;
    background-color: white;
    position: absolute;
    height: 150px;
    width: 150px;
    top: 50%;
    left: 92px;
    transition: height 5s ease, width 5s ease;
    transform: translate(-50%, -50%);
    z-index: 0;
    opacity: 0.5;
    &:before,
    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        border-radius: 50%;
        border: 1px solid white;
    }
    &:before {
        animation: ripple 2s linear infinite;
    }
    &:after {
        animation: ripple 2s linear 1s infinite;
    }
    &.b-round {
        height: 230px;
        width: 230px;
        left: 50%;
    }
}

.animation-circle-inverse {
    i {
        background: rgba($theme-color1, 0.15);
        right: 0;
        box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11);
        position: absolute;
        height: 100%;
        width: 100%;
        border-radius: 100%;
        opacity: 0.3;
        top: 0;
        transform: scale(1);
        animation: ripple1 3s linear infinite;
        &:nth-child(2) {
            animation: ripple2 3s linear infinite;
        }
        &:nth-child(3) {
            animation: ripple3 3s linear infinite;
        }
    }
}

.animated-section {
    overflow: hidden;
    position: relative;
    .animation-section {
        opacity: 0.3;
        .round {
            width: 12px;
            height: 12px;
            background-color: transparent;
            border: 2px solid $theme-color1;
            border-radius: 100%;
            position: absolute;
            animation: myOrbit 9s linear infinite;
            &.r-2 {
                width: 15px;
                height: 15px;
            }
            &.r-y {
                border-color: #e8b10b;
            }
        }
        .square {
            width: 12px;
            height: 12px;
            background-color: transparent;
            position: absolute;
            border: 2px solid $theme-color1;
            animation: animationFramesTwo 50s infinite linear;
            &.s-2 {
                width: 15px;
                height: 15px;
            }
        }
        .cross {
            height: 12px;
            width: 12px;
            position: absolute;
            animation: animationFramesTwo 50s infinite linear;
            &:after,
            &:before {
                position: absolute;
                content: " ";
                height: 12px;
                width: 2px;
                background-color: $theme-color1;
            }
            &:after {
                transform: rotate(-45deg);
            }
            &:before {
                transform: rotate(45deg);
            }
        }
        .po-1 {
            top: 50%;
            left: 5%;
        }
        .po-2 {
            right: 5%;
            bottom: 7%;
        }
        .po-3 {
            top: 20%;
            right: 8%;
        }
        .po-4 {
            bottom: 5%;
            left: 30%;
        }
        .po-5 {
            right: 20%;
            top: 10%;
        }
        .po-6 {
            top: 2%;
            left: 47%;
        }
        .po-7 {
            right: 1%;
            top: 53%;
        }
        .po-8 {
            bottom: 1%;
            right: 27%;
        }
        .po-9 {
            top: 24%;
            right: 30%;
        }
        .po-10 {
            top: 5%;
            left: 15%;
        }
        .po-11 {
            bottom: 32%;
            left: 36%;
        }
        .po-12 {
            left: 2%;
            bottom: 10%;
        }
    }
}

.zig-zag-effect {
    position: relative;    
    &:after {
        position: absolute;
        content: "";
        background: url("../images/zig-zag-1.png");
        bottom: 0;
        width: 100%;
        height: 5px;
    }
    &:before {
        position: absolute;
        content: "";
        background: url(../images/zig-zag-1.png);
        top: 0;
        width: 100%;
        height: 5px;
        transform: rotate(180deg);
    }
    &.detail-section{
        .detail-box{
            .upper-part{
                margin-bottom: 20px;
