}

// flight fly animation
@mixin matrix($x,
    $y,
    $angle) {
    transform: translate($x, $y) rotate($angle);
}

.flight {
    width: calc(140px + (200 - 140) * ((100vw - 320px) / (1920 - 320)));
    height: calc(70px + (100 - 70) * ((100vw - 320px) / (1920 - 320)));
    background: url("../images/flights/airliner.png") center no-repeat;
    background-size: 100%;
    position: absolute;
    bottom: -4px;
    right: 0;
    &.run {
        animation: landFlight linear 10s;
    }
}

@keyframes landFlight {
    0% {
        @include matrix(0,
            0,
            0deg);
    }
    20% {
        @include matrix(-600px,
            -1px,
            0deg);
    }
    50% {
        @include matrix(-650px,
            -2px,
            0deg);
    }
    58% {
        @include matrix(-700px,
            -5px,
            10deg);
    }
    59% {
        @include matrix(-710px,
            -10px,
            15deg);
    }
    60% {
        @include matrix(-720px,
            -12px,
            20deg);
    }
    70% {
        @include matrix(-800px,
            -50px,
            20deg);
    }
    80% {
        @include matrix(-900px,
            -100px,
            20deg);
    }
    100% {
        @include matrix(-2200px,
            -750px,
            20deg);
    }
}

.plane-animation {
    animation-name: plane-animation;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

@keyframes plane-animation {
    0% {
        //margin-top: 135px;
        transform: translateY(-50px);
    }
    50% {
        //margin-top: 165px;
        transform: translateY(0);
    }
    100% {
        //margin-top: 130px;
        transform: translateY(-50px);
    }
}


/*=====================
28. Theme setting css
==========================*/

.theme-setting {
    position: fixed;
    top: 50%;
    transform: translateY(-50%) rotate(90deg);
    right: -52px;
    display: flex;
    z-index: 10;
    .dark,
    .rtl {
        margin-left: 5px;
        margin-right: 5px;
    }
    .rtl {
        .tgl-skewed {
            &:checked {
                +.tgl-btn {
                    background: #dcdcdc;
                }
            }
        }
    }
    .tgl {
        display: none;
        +.tgl-btn {
            outline: 0;
            display: block;
            width: 4em;
            height: 2em;
            position: relative;
            cursor: pointer;
            user-select: none;
            &:before {
                display: none;
            }
            &:after {
                left: 0;
            }
            &:after,
            &:before {
                position: relative;
                display: block;
                content: "";
                width: 50%;
                height: 100%;
            }
        }
        &:checked {
            +.tgl-btn {
                &:after {
                    left: 50%;
                }
            }
        }
    }
    .tgl-skewed {
        +.tgl-btn {
            overflow: hidden;
            transform: skew(-10deg);
            backface-visibility: hidden;
            transition: all .2s ease;
            font-family: sans-serif;
            background-color: #424242;
            outline: 0;
            display: block;
            width: 4em;
            height: 2em;
            position: relative;
            cursor: pointer;
            user-select: none;
            &:after,
            &:before {
                transform: skew(10deg);
                display: inline-block;
                transition: all .2s ease;
                width: 100%;
                text-align: center;
                position: absolute;
                line-height: 2em;
                font-weight: bold;
                color: #000;
                text-shadow: 0 1px 0 rgba(0, 0, 0, .4);
            }
            &:after {
                left: 100%;
                content: attr(data-tg-on);
            }
            &:before {
                left: 0;
                color: white;
                content: attr(data-tg-off);
            }
            &:active {
                background: #888;
                &:before {
                    left: -10%;
                }
            }
        }
        &:checked+.tgl-btn {
            background: #ffffff;
            &:before {
                left: -100%;
            }
            &:after {
                left: 0;
            }
            &:active:after {
                left: 10%;
            }
        }
    }
    &.back {
        z-index: 1;
    }
    &.one-setting {
        right: -20px;
    }
}